.pom-video-container{
	position:relative;
	padding-bottom:56.25%;
	padding-top:30px;
	height:0;
	overflow:hidden;
	}
.pom-video-container-outer{
	max-width: 640px;
	}

.pom-video-container iframe,.pom-video-container object,.pom-video-container embed{
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
	}