@mixin button-link($icon: null, $background-color: c(cyan1)) {
  @include font($size: 14px, $family: $font-family-sans, $weight: 700);
  display: if($icon, inline-block, inline-flex);
  position: relative;
  padding: rem-calc(5px 10px);
  border-bottom-width: 0; // Turn off anchor tag border if present.
  background-color: $background-color;
  color: c(white1);
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: uppercase;

  &:link,
  &:visited,
  &:focus {
    background-color: $background-color;
    color: c(white1);
  }

  @if ($icon is not null) {
    @supports (display: inline-flex) {
      display: inline-flex;
      align-items: center;
      justify-content: flex-start;

      &::after {
        margin-left: .5em;
        color: $color-chevron;
        font-family: $font-awesome;
        font-size: .86em;
        line-height: 1;
        content: $icon;
      }
    }
  }
}

@mixin more-link($background-color: c(lavender1)) {
  @include icon-button($icon: '\f054', $background-color: $background-color);
}

// convenience method to attach an icon, in this case an angle bracket
@mixin icon-button($icon: '\f061', $background-color: c(cyan1)) {
  @include button-link($icon: $icon, $background-color: $background-color);
}

@mixin content-button($icon: '\f061', $background-color: c('lavender1')) {
  @include button-link($icon: $icon, $background-color: $background-color);
  font-size: rem-calc(18);
  font-weight: 300;
  text-transform: none;

  &::after {
    font-weight: normal;
  }
}

@mixin underlined-link($color: c(canvas1)) {
  border-bottom: 2px solid $color;
  text-decoration: none;

  &:hover {
    border-bottom-color: currentColor;
  }
}
