@mixin link-style {
  border-bottom: 2px solid $background-button;
}

@mixin background-pattern {
  background: url("../../images/bkg-pttrn-lt-grey.png") repeat 0 0 transparent;
}

img.right {
  float: right;
  margin: 0 0 25px 25px;
}

img.left {
  float: right;
  margin: 0 25px 25px 0;
}

@mixin button-style {
  @include font($size: 18px, $line-height: 1, $weight: 700, $family: $font-family-sans);
  font-size: rem-calc(18px);
  appearance: initial;
  background: $background-button;
  border: 0px none;
  color: #fff;
  display: inline-block;
  letter-spacing: 1px;
  padding: 17px 49px 17px 15px;
  position: relative;
  text-transform: uppercase;
  -webkit-appearance: initial;
  -moz-appearance: initial;

  &:after {
    color: $color-chevron;
    content: "\f054";
    font-family: "FontAwesome";
    font-size: rem-calc(16px);
    margin-top: -8px;
    position: absolute;
    right: 19px;
    top: 50%;
  }

  &:hover {
    opacity: 0.7;
  }
}

@mixin large-button-style {
  font-size: rem-calc(26px);
  line-height: 26px;
  padding: 23px 46px 23px 25px;

  &:after {
    font-size: rem-calc(23px);
    margin-top: -11.5px;
    right: 15px;
    top: 50%;
  }
}

@mixin medium-button-style {
  font-size: rem-calc(23px);
  line-height: 22px;
  padding: 20px 43px 20px 20px;

  &:after {
    font-size: rem-calc(18px);
    margin-top: -9px;
    right: 12px;
    top: 50%;
  }
}

@mixin extra-small-button-style {
  font-size: rem-calc(14px);
  line-height: 14px;
  padding: 14px 29px 14px 14px;

  &:after {
    font-size: rem-calc(12px);
    line-height: 12px;
    margin-top: -6px;
    right: 12px;
    top: 50%;
  }
}

@mixin purple-button-style {
  background-color: #b072b3;
}

@mixin green-button-style {
  background-color: #3dae70;
}

@mixin orange-button-style {
  background-color: #ea7e24;
}

@mixin orange {
  @include orange-button-style;
}

@mixin submit-button-style {
  @include font($size: 14px, $line-height: 1, $weight: 700, $family: $font-family-sans);
  appearance: initial;
  background: url("../../images/bkg-submit-arrow.png") no-repeat 87% 14px #b072b3;
  border: 0px none;
  color: #fff;
  display: inline-block;
  letter-spacing: 1px;
  padding: 14px 29px 14px 14px;
  position: relative;
  text-transform: uppercase;
  -moz-appearance: initial;
  -webkit-appearance: none;
  -webkit-border-radius: 0;

  &:hover {
    opacity: 0.7;
  }
}

@mixin upload-button-style {
  @include button-style;
  background-image: url("../../images/bkg-upload-icon.png");
  background-position: 18px 45%;
  background-repeat: no-repeat;
  font-size: rem-calc(14px);
  padding: 15px 20px 15px 53px;
}

@mixin pagination-style {
  li {
    @include font($size: 14px, $weight: 700, $family: $font-family-sans);
    background: none !important;
    display: inline-block;
    margin: 0 1px;
    padding: 0;

    a {

      &:link,
      &:visited,
      &:focus {
        border: 0px none;
        color: #000000;
        font-weight: normal;
        display: inline-block;
        line-height: 14px;
        padding: 9px 13px;
        text-decoration: none;
        text-transform: uppercase;
        -moz-text-decoration-line: none;
      }

      &:hover {
        background-color: $background-pagination-hover;
        color: #fff;
      }
    }

    &.pager-current {
      background-color: $background-pagination-hover !important;
      color: #fff;
      padding: 7px 12px;
      vertical-align: middle;

      a {

        &:link,
        &:visited,
        &:focus {
          background-color: $background-pagination-hover;
          color: #fff;
        }
      }
    }

    &.pager-next a {

      &:link,
      &:visited,
      &:focus {
        @include icon-button;
      }
    }
  }
}



@mixin list-view-content-styles {
  background-color: #fff;
  border-top: 6px solid $color-listing-border;
  margin-bottom: 25px;

  .field-taxonomy,
  .field-type-taxonomy-term-reference {
    font-size: rem-calc(12px);
    color: $color-link;
    text-transform: uppercase;

    a {
      text-decoration: none;
    }
  }

  .field-title,
  .field-name-title-field,
  .views-field-title {
    font-family: $font-family-sans;

    a {
      color: $color-link-listed;
      border-bottom: 2px solid $color-link-underline;
    }
  }

  .field-name-body {
    font-size: rem-calc(18px);
    line-height: 1.2em;
  }

  .field-thumbnail,
  .field-name-field-image {
    img {
      display: block;
    }
  }

  .field-name-field-event-date,
  .field-name-field-publish-date {
    font-family: $font-family-sans;
    font-size: rem-calc(16px);
    color: $color-form-text;
  }

  .field-name-field-event-time {
    display: inline-block;
  }

  .field-name-field-location {
    font-family: $font-family-sans;
    font-size: rem-calc(16px);
    color: $color-form-text;
  }
}

@mixin view-style {
  .item-list .views-row {
    list-style: none;
    margin-left: 0;
  }

  .view-header {
    h3 {
      margin-bottom: 0px;
    }

    a.more-link {
      position: absolute;
      right: 0;
      top: -15px;
    }
  }

  .views-row {
    @include list-view-content-styles;
  }

  .view-filters {
    .views-exposed-form {
      margin-bottom: 25px;

      label {
        display: inline-block;
        margin-right: 10px;
      }

      .views-widget {
        display: inline-block;
      }
    }

    .views-exposed-widget .form-submit {
      margin-top: 0px;
      padding: 10px 30px;
    }
  }
}

@mixin listing-view-style {
  .group-listing-info {
    margin-left: 272px;
  }

  .field-title,
  .field-name-title-field {
    margin-bottom: 10px;
    margin-left: 0;
    padding-right: 50px;
    //     padding-top: 15px;
  }

  .field-name-body {
    margin-left: 0;
    padding-right: 50px;
  }

  .field-thumbnail,
  .field-name-field-image {
    float: left;
    margin-right: 20px;

    img {
      display: block;
    }
  }

  .field-name-field-event-date,
  .field-name-field-publish-date {
    margin-bottom: 15px;
    margin-left: 0;
  }
}

@mixin grid-view-style {
  .views-row {
    border-top: 0px none;
    display: inline-block;
    vertical-align: top;
    width: 31.8%;
    min-height: 485px;

    &:first-child,
    &:last-child {
      margin: 0;
    }

    .field-type-image {
      border-bottom: 6px solid #cccccc;
      float: none;
      margin-right: 0;

      img {
        display: block;
      }
    }

    .field-name-title-field {
      margin-bottom: 15px;
    }

    .field-type-datetime {
      margin-bottom: 15px;
    }

    .group-inner-wrapper {
      padding: 10px 20px 0px;
    }
  }

  .views-row-2,
  .views-row-5,
  .views-row-8,
  .views-row-11 {
    margin-left: 1.5%;
    margin-right: 1.5%;
  }
}

@mixin grid-image-event-view-style {
  .views-row {
    .node {
      padding: 0;
    }
  }

  .group-list-content-wrapper {
    min-height: 196px;
    padding: 10px 20px 0;
  }
}

@mixin flexslider-style {
  border: 0px none;
  box-shadow: none;
  margin: 0;

  .slides li {
    margin: 0;

    .field-name-field-image {
      margin-bottom: 0px;
      float: none;
    }

    .group-content {
      margin: 0 auto;
      max-width: 1000px;
      position: relative;

      .field-name-field-slide-body {
        background-color: rgba(255, 255, 255, 0.8);
        bottom: 80px;
        max-width: 357px;
        padding: 20px 20px 25px;
        position: absolute;
        right: 0;

        .not-front & {
          bottom: 30px;
        }

        h2 {
          font-size: rem-calc(28px);
          color: $color-link;
          line-height: 1.05em;
          margin: 0 0 10px;
        }

        p {
          font-size: rem-calc(18px);
          margin-bottom: 0px;
        }
      }
    }
  }
}

@mixin event-view-style {
  .views-row {
    display: inline-block;
    min-height: 210px;
    vertical-align: top;
    width: 46.5%;

    .node {
      padding: 15px 20px 20px;
    }

    .field-name-title-field {
      margin-bottom: 15px;
      margin-left: 0;
      padding-top: 0px;
    }

    .field-name-field-event-date {
      line-height: 16px;
      margin-bottom: 0px;
      margin-left: 0;
    }
  }

  .views-row-odd {
    margin-right: 3%;
  }
}

@mixin event-footer-view-style {
  .views-row {
    width: 31.8%;
    min-height: 196px;
  }

  .views-row-odd {
    margin-right: 0;
  }

  .views-row-2,
  .views-row-5,
  .views-row-8,
  .views-row-11 {
    margin-left: 1.5%;
    margin-right: 1.5%;
  }

  body.page-node-1 &,
  .front & {
    .views-row {
      width: auto;
    }

    .views-row-odd {
      margin: 0;
    }

    .views-row-even {
      margin: 0;
    }
  }
}

@mixin flexslider-control-paging-style {
  //   bottom: 65px;
  left: 0;
  margin: 0px auto;
  //   margin-left: -370px;
  //   padding: 0 5px 0 25px;
  text-align: left;
  width: auto;
  z-index: 10;

  li {
    line-height: 11px;
    margin: 0 3px;
    padding: 7px 0px;

    a {
      background: none repeat scroll 0 0 #ffe121;
      border: 2px solid #ffe121;
      box-shadow: none;
      height: 9px;
      width: 9px;

      &.flex-active {
        background-color: $color-link;
        border-color: $color-link;
      }

      &:hover {
        background: none no-repeat 0 0 $color-link;
        border-color: $color-link;
      }
    }
  }
}

@mixin flexslider-direction-nav-style {
  li {
    margin: 0;

  }

  a {
    display: block;
    margin: 0px;
    height: 56px;
    opacity: 0.4 !important;
    transition: none;
    width: 37px;

    &:before {
      background: url("../../images/bkg-slideshow-arrows.png") no-repeat scroll 0 0 transparent;
      content: "";
      height: 56px;
      width: 37px;
    }

    &.flex-next:before {
      background-position: -35px 0;
      content: "";
    }

    &:hover {
      opacity: 1 !important;
    }
  }

  .flex-next {
    opacity: 1 !important;
    right: 5%;
    top: 74.5%;
  }

  .flex-prev {
    opacity: 1 !important;
    left: 5%;
    top: 74.5%;
  }

  .flex-disabled {
    display: none !important;
  }
}

@mixin flexslider-pauseplay-style {
  display: none;
  bottom: 80px;
  left: 50%;
  margin: 0 0 0 -500px;
  position: absolute;
  width: 1000px;

  a {
    bottom: 5px;
    color: #fff;
    left: 8px;
    opacity: 1;

    &:before {
      font-size: rem-calc(13px);
    }
  }
}

@mixin bucket-style {
  @include background-pattern;
  @include font($size: 24px, $line-height: 1.2, $weight: 400, $family: $font-family-sans);
  border-top: 6px solid $color-border-bucket-blue;
  display: inline-block;
  margin: 0 0.7%;
  min-height: 130px;
  padding: 10px 20px;
  vertical-align: top;
  width: 18.5%;

  a {
    border-bottom: 0px none;
  }

  &:first-child {
    margin-left: 0;
  }

  &:last-child {
    margin-right: 0;
  }
}

@mixin featured-view-style {

  .view,
  .content {
    background-color: #fff;
    border-bottom: 4px solid $background-pagination-hover;
    min-height: 368px;
    padding-bottom: 15px;
    position: relative;

    .views-row {
      border-top: 0px none;
      margin-bottom: 0px;
    }

    .field-type-image {
      border-bottom: 13px solid $background-pagination-hover;
      margin-bottom: 20px;
      max-width: 100%;

      a {
        display: block;
      }

      img {
        max-width: 100%;
        display: block;
      }
    }

    .field-name-title-field,
    .field-name-field-event-link,
    .views-field-title,
    .field-type-link-field {
      font-family: $font-family-sans;
      line-height: 1.3em;
      padding: 0 25px;

      a {
        border-bottom: 1px solid $background-pagination-hover;
        color: $color-link-listed;
      }
    }

    .view-footer {
      bottom: 5px;
      position: absolute;
      right: 25px;

      p {
        margin: 0;
      }

      a {
        @include font($size: 14px, $weight: 700, $family: $font-family-sans);
        color: $color-link;
        padding-right: 15px;
        position: relative;
        text-transform: uppercase;

        &:after {
          content: "\f054";
          font-family: "FontAwesome";
          font-size: rem-calc(12px);
          position: absolute;
          top: -7px;
          right: 0;
        }
      }
    }
  }

  .view.view-events .views-row .field-type-datetime,
  .content .field-type-datetime,
  .content .field-name-field-event-date-string {
    color: $color-header-3;
    font-family: $font-family-serif;
    font-size: rem-calc(28px);
    font-style: italic;
    line-height: 28px;
    margin: 0 0 5px;
    padding: 0 25px;
  }
}

@mixin node-info-style {
  font-family: $font-family-sans;
  font-size: rem-calc(16px);
  color: $color-form-text;
}

@mixin list-link-style {

  &:link,
  &:visited {
    border-bottom: 2px solid $color-link-underline;
    color: $color-link-listed;
  }

  &:hover,
  &:focus {
    color: $color-link-listed-hover;
    border-bottom: 2px solid transparent;
  }

  &.active,
  &:active {
    color: $color-link-listed;
  }
}

