@mixin article-event-styles {
  .quickedit-form form {
    padding: 0;
  }

  .quickedit-form-container {
    .field-name-field-banner {
      margin: 0;
    }

    .form-wrapper .form-wrapper {
      padding: 0;
    }
  }

  .field-name-field-banner {
    margin: 0 -15px -80px;
    position: relative;
    z-index: 1;

    img {
      display: block;
    }
  }

  .group-body-wrapper {
    border-top: 6px solid $color-link-underline;
    background-color: #fff;
    margin: 0 auto;
    position: relative;
    z-index: 2;
  }

  .group-info {
    @include node-info-style;
    margin-bottom: 30px;

    .field {
      display: inline-block;
    }

    span.field-label {
      padding-right: .1em;
    }

    .field-type-datetime {
      &:after {
        content: "\00a0";
      }
    }

    .field-name-field-photo-caption {
      &:after {
        content: "\00a0";
      }
    }
  }

  .field-name-body {
    blockquote {
      font-family: $font-family-sans;
      font-size: rem-calc(28px);
      color: $color-form-text;
      padding: 0 70px;

      p:first-of-type:first-letter {
        color: $color-form-text;
        float: none;
        font-size: rem-calc(28px);
        font-weight: normal;
        padding-right: 0px;
        padding-top: 0px;
      }

      &:before {
        background-image: url("../../images/bkg-article-quote-open.png");
        left: -125px;
      }

      &:after {
        background-image: url("../../images/bkg-article-quote-close.png");
        right: -125px;
      }
    }
  }
}

@mixin sidebar-filter-label-style {
  font-size: rem-calc(14px);
  background: $color-link;
  color: #fff;
  cursor: pointer;
  padding: 4px 12px;
  text-transform: uppercase;

  &:after {
    content: "\f068";
    font-family: $font-awesome;
    font-size: rem-calc(12px);
    float: right;
    color: $color-chevron;
  }

  &.closed:after {
    content: "\f067";
  }
}

@mixin sidebar-filter-label-style-closed-default {
  &:after {
    content: "\f067";
  }

  &.closed:after {
    content: "\f068";
  }

}

@mixin filtered-list-header-style {
  background-color: #ea7e24;
  color: #fff;
  display: inline-block;
  left: 0;
  margin: 0;
  min-width: 40px;
  padding: 4px 10px 3px;
  position: absolute;
  text-align: center;
  top: -25px;
}

@mixin short-caption-slideshow-style {
  border-bottom: 0px none;

  .slide-caption-wrapper {
    width: 100%;
    background: $background-body;
    line-height: 1;
    text-align: center;

    .slide-caption {
      padding: 15px 25px;
      font-size: rem-calc(40px);
      padding-bottom: 40px;
    }

    .slide-caption-inner-wrapper {
      left: 0;
      bottom: 0;
      position: inherit;

      .slide-links {
        padding-left: 0;
      }
    }
  }

  .flexslider {
    background: $background-body;
    border-radius: 0px;

    .flex-control-paging {
      margin-left: 0;
      padding: 0;
      bottom: 5px;
      left: 45%;
    }

    .slides {
      padding-bottom: 15px;
    }

    .flex-control-nav {
      position: static;
      text-align: center;
      padding-bottom: 15px;
    }

    .flex-direction-nav {

      .flex-prev,
      .flex-next {
        top: 19%;
      }
    }
  }
}

@media (max-width: 768px) {
  .container-inline-block {

    &.column-2,
    &.column-3 {
      >div {
        width: 100%;
        display: block;
        padding-right: 0;
      }
    }
  }
}

@mixin bullet-list-style {

  ul {
    li {
      background: url("../../images/bkg-list-bullet.jpg") no-repeat 0 12px transparent;
      list-style: none;
      margin-bottom: 12px;
      padding-left: 15px;
      //bullet points hiding behind image on insets
      overflow: hidden;
      line-height: 1.3;

      ul {
        margin-bottom: 5px;
      }

      li {
        background-image: url("../../images/bkg-list-bullet-2.jpg");
        background-position: 0 12px;

        li {
          background-image: url("../../images/bkg-list-bullet-3.jpg");
          background-position: 0 12px;

          li {
            background-image: url("../../images/bkg-list-bullet-4.jpg");
            background-position: 0 12px;
          }
        }
      }

      a {
        @include list-link-style;
      }
    }

    &.pagination {
      @include pagination-style;
    }
  }
}

@mixin element-invisible {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  height: 1px;
}

// layout
@mixin clearfix {
  &:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
  }
  .lt-ie7 & {
    height: 1%;
  }
  .lt-ie8 & {
    min-height: 1%;
    zoom: 1;
  }
}

@mixin inline-block {
  display: inline-block;
  vertical-align: top;
  zoom: 1;

  .lt-ie8 & {
    display: inline;
  }
}

@mixin green-button-link {
  a {
    border-bottom: none;
    color: #fff;
    text-transform: uppercase;
    font-size: rem-calc(16px);
    padding: 5px 10px;

    &:after {
      font-family: $font-awesome;
      color: $color-yellow-accent;
      padding-left: 10px;
      font-size: rem-calc(12px);
    }
  }
}


