// Global site stucture such as html, body, and region styles etc...

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 21px;
}

body {
  min-width: rem-calc(320px);
  margin-bottom: 0;
  font-family: $font-family-serif;

  * {
    box-sizing: border-box;
  }
}


#mimemail-body {
  background-color: white;
}

.highlighted-wrapper {
  position: relative;
  z-index: 1;

  .front.not-logged-in & {
    padding-top: 75px;
  }
}

.region-highlighted {
  margin: 0 auto;
  max-width: 1300px;
  position: relative;
}

.title-wrapper {
  background: url("../../images/bkg-title-pattern.jpg") repeat-x 0 5px transparent;

  .not-front & {
    padding-bottom: 30px;
    padding-top: 45px;
  }

  .not-front.not-logged-in & {
    padding-top: 50px;
  }

  .logged-in & {
    background-position: 0 0;
  }
}

.content-wrapper {
  margin: 0 auto;
  max-width: 1300px;
}

.has-footer-calendar {
  #content {
    min-height: 0;
    padding: 0;
  }
}

#content {
  min-height: 300px;
  padding: 0px 0px 55px;

  .sidebar-second & {
    padding-right: 40px;
  }

  body.page-node-1 &,
  .front & {
    min-height: 0px;
    padding: 0px;
  }
}

.region-sidebar-second {
  padding: 0px;
}

.footer-wrapper {
  background-color: #fff;
  margin: 0 auto;
  clear: both;
}

.footer-inner-wrapper {
  background: url("../../images/bkg-pttrn-lt-grey.png") repeat 0 0 transparent;
  margin: 0 auto;
  max-width: 1300px;

  body.page-node-1 &,
  .front & {
    padding-top: 25px;
  }
}

.bottom-wrapper {
  position: relative;
  background: url("../../images/bkg-bttm-texture-left.jpg") no-repeat left bottom $color-link-listed;
  border-top: 5px solid #018abb;
  color: #fff;
  font-family: $font-family-serif;
  font-weight: 400;
  height: 100%;
  padding-top: 35px;
  z-index: 2;

  &:after {
    background: url("../../images/bkg-bttm-texture-right2.jpg") no-repeat 0 3px transparent;
    content: "";
    height: 297px;
    position: absolute;
    right: 0;
    bottom: 0;
    width: 457px;
    z-index: -100;
  }

  p,
  a,
  ul li {
    color: #fff;
  }
}

body.page-node-1,
.front {
  h1.title {
    display: none;
  }
}

h2.block-title {
  @include font($size: 21px, $family: $font-family-sans, $weight: 400);
  text-transform: uppercase;
}

.two-col-layout {
  display: block;
  overflow: auto;

  .col-1,
  .col-2 {
    width: 46%;
    float: left;
    margin-right: 2%;
  }
}

p#skip-link {
  margin: 0;
}

ul {
  padding: 0;

  li {
    list-style-position: inside;
    margin-bottom: 5px;
  }
}


.sub-note {
  font-family: $font-family-sans;
  font-style: italic;
  font-size: rem-calc(16px);
  color: $color-form-text;
}


.field-name-field-article-category {
  &:last-of-type {
    a {
      &:after {
        content: ' ';
      }
    }
  }

  display: inline;

  a {
    &:after {
      color: $color-form-text;
      content: ',';
      font-weight: bold;
    }
  }
}

article.node .field-name-body {
  @include bullet-list-style;
}

ul.pagination,
ul.pager {
  @include pagination-style;
  width: 50%;
  float: right;
}

.item-list .pager li {
  padding: 0;
}

ol {
  padding: 0;
  padding-left: 5px;
}

article.node .field-name-body,

.ordered-list-style {
  ol {
    ul {
      padding-top: 10px;
      padding-left: 20px;
    }

    counter-reset: item;

    >li {
      display: block;
      padding-left: 15px;

      &:before {
        content: counter(item) ". ";
        counter-increment: item;
        color: #60a7d5;
        @include font($family: $font-family-sans, $weight: 700);
      }
    }
  }
}

article.node .field-name-body ul {
  padding-left: 20px;
}


@mixin table-style {
  font-size: rem-calc(16px);
  border: 0px none;
  font-family: $font-family-sans;
  margin-bottom: 25px;

  a {
    color: #20438f;
  }

  tbody {
    border-top: 0px none;
  }

  th {
    font-size: rem-calc(14px);
    background-color: $color-link;
    border: 0px none;
    color: #fff;
    font-weight: bold;
    padding: 10px 10px;
    text-align: center;
    text-transform: uppercase;

    &:first-child {
      border-radius: 6px 0px 0px 0px;
    }

    &:last-child {
      border-radius: 0px 6px 0px 0px;
    }
  }

  tr {
    background-color: transparent;

    &.even,
    &.odd {
      background-color: transparent;
      border: 0px none;
    }

    &:nth-child(even) {
      background-color: #f2efe2;
    }
  }

  td {
    border: 0px none;
    padding: 15px 10px;
    text-align: center;

    &:last-child {
      border-right: 0px none;
    }
  }

  &.second-style {
    font-size: rem-calc(18px);

    th {
      font-size: rem-calc(21px);
      background-color: #e5e5e5;
      border-right: 2px solid #fff;
      color: #171614;
      font-weight: normal;
      padding: 5px 10px 10px;
      text-align: left;
      text-transform: none;

      &:first-child {
        border-radius: 0px 0px 0px 0px;
      }

      &:last-child {
        border-right: 0px none;
        border-radius: 0px 0px 0px 0px;
      }
    }

    tr {
      background-color: transparent;

      &.even,
      &.odd {
        background-color: #f6f6f6;
        border: 0px none;
      }

      &:nth-child(even) {
        background-color: transparent;
      }
    }

    td {
      border-right: 2px solid #fff;
      padding-bottom: 20px;
      text-align: left;
      vertical-align: top;

      &:last-child {
        border-right: 0px none;
      }

      span.small-text {
        font-size: rem-calc(16px);
        display: block;
      }
    }
  }
}

table {
  @include table-style;
}

@mixin table-style-two {
  @include table-style;
  font-size: rem-calc(18px);

  th {
    font-size: rem-calc(21px);
    background-color: #e5e5e5;
    border-right: 2px solid #fff;
    color: #171614;
    font-weight: normal;
    padding: 5px 10px 10px;
    text-align: left;
    text-transform: none;

    &:first-child {
      border-radius: 0px 0px 0px 0px;
    }

    &:last-child {
      border-right: 0px none;
      border-radius: 0px 0px 0px 0px;
    }
  }

  tr {
    background-color: #fff;
    ;

    &.even,
    &.odd {
      background-color: #f6f6f6;
      border: 0px none;
    }

    &:nth-child(even) {
      background-color: #f6f6f6;
    }
  }

  td {
    border-right: 2px solid #fff;
    padding-bottom: 20px;
    text-align: left;
    vertical-align: top;

    &:last-child {
      border-right: 0px none;
    }

    span.small-text {
      font-size: rem-calc(16px);
      display: block;
    }
  }
}

// Keep for non-jquery select boxes (minimal style change on page load)

// jQuery Select Boxes (uses selectbox module)

.selectBox-dropdown,
.selectBox {
  font-family: $font-family-sans;
  -webkit-appearance: none;
  -moz-appearance: none;
  cursor: pointer;
  text-indent: 1px;
  text-overflow: '';
  //   padding: 6px 0 3px 0;
  border: 5px solid #3FAD70;
  border-radius: 0;
  -moz-box-shadow: inset 0 7px 9px -7px rgba(0, 0, 0, 0.4);
  -webkit-box-shadow: inset 0 7px 9px -7px rgba(0, 0, 0, 0.4);
  box-shadow: inset 0 7px 9px -7px rgba(0, 0, 0, 0.4);
  line-height: 1;
  width: auto !important;

  &:focus {
    border-color: #3FAD70;
  }

  &:hover {
    color: #000;
  }

  option {
    padding: 10px;
  }
}

.selectBox-dropdown .selectBox-arrow {
  background: url("../../images/select_down.png") 50% center no-repeat;
}

.selectBox-dropdown-menu {
  font-family: $font-family-sans;
  margin-top: 10px;
  box-shadow: none;
  border-radius: 0;
  border: none;
  border-bottom: 1px solid #ccc;

  li {
    border: 1px solid #ccc;
    border-bottom: none;
    padding: 11px 9px;

    a {
      color: #000;
      cursor: pointer;
      font-size: rem-calc(18px);
    }
  }

  li:nth-child(even) {
    background: #fbfaf6;
  }

  li:last-child {
    border-bottom: 1px solid #ccc;
  }
}

.selectBox-inline {
  li {
    background: none !important;
    padding-left: 0 !important;

    &:hover {
      color: #000 !important;
      border-bottom: 0 !important;
    }

    a {
      color: #000;
    }
  }
}

.selectBox-options {
  li {
    a {
      &:hover {
        border-bottom: 0 !important;
        color: #aaa !important;
      }
    }
  }
}


// Admin Tabs Theming
.logged-in {
  .tabs-primary {
