    }
  }
}

// Throbber
.views-submit-button {
  .ajax-progress .throbber {
    position: absolute;
    top: -27px;
    right: 38px;
  }
}

.ajax-progress .throbber {
  background: transparent url("../../images/throbber.gif") no-repeat 0px -21px;
}

form {
  font-size: rem-calc(18px);
  color: $color-form-text;
  font-family: $font-family-sans;
  font-weight: normal;

  input[type='search'],
  input[type='text'],
  input[type='email'],
  input[type='password'] {
    border: 1px solid $color-form-border;
    border-radius: 0;
    box-sizing: border-box;
    color: $color-form-text;
    padding: 12px 16px;
    width: 100%;
    -webkit-appearance: none;

    .block & {
      width: 90%;
    }

    &:focus {
      //       border: 6px solid #40ad70;
      -webkit-box-shadow: inset 0px 11px 18px -14px rgba(0, 0, 0, 0.25);
      -moz-box-shadow: inset 0px 11px 18px -14px rgba(0, 0, 0, 0.25);
      box-shadow: inset 0px 11px 18px -14px rgba(0, 0, 0, 0.25);
      //       padding: 7px 10px;
      outline: none;
    }
  }

  input[type='checkbox'],
  input[type='radio'] {
    color: $color-form-text;
    margin-right: 10px;
  }

  input[type='checkbox'] {
    &.depth-1 {
      margin-left: 10px;
    }

    &.depth-2 {
      margin-left: 20px;
    }

    &.depth-3 {
      margin-left: 30px;
    }

    &.depth-4 {
      margin-left: 40px;
    }
  }

  textarea {
    border: 1px solid $color-form-border;
    color: $color-form-text;
    padding: 10px;
  }

  .form-item {
    margin: 0 0 1.1em;

    &.form-type-textfield,
    &.form-type-password {
      width: 50%;
    }

    .form-textarea,
    .form-text {
      border-radius: 0;
    }

    .description {
      color: #666666;
      margin-top: 5px;
    }

    .form-type-radio,
    .form-type-checkbox {
      margin-bottom: 5px;
    }

    select {
      font-size: rem-calc(15px);
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      border: 1px solid $color-form-border;
      border-radius: 0;
      color: #777777;
      padding: 10px 45px 9px 10px;
      width: 100%;
    }
  }

  .link-field-column {
    display: inline-block;
    float: none;
    width: auto;
  }

  .container-inline-date .form-item .form-item {
    display: inline-block;
    float: none;
    margin-right: 15px;
  }

  .date-float .form-type-checkbox {
    margin: 0;
  }

  fieldset.form-wrapper {
    padding: 0;
  }

  .form-submit,
  input[type='submit'] {
    @include submit-button-style;
  }

  input#edit-submitted-file-upload-button,
  .image-widget input[type='submit'] {
    @include upload-button-style;
  }

  .form-item.form-type-select {
    display: inline-block;
  }

  input.webform-calendar {
    padding: 42px 0 0 10px;
  }
}

