}

blockquote {
  font-style: normal;
  margin: 0 0 40px;
  padding: 0 100px;
  position: relative;

  p {
    &:before {
      background: url("../../images/bkg-quote-open.png") no-repeat 0 0 transparent;
      content: "";
      height: 51px;
      left: 0;
      position: absolute;
      top: 0;
      width: 65px;
    }

    &:after {
      background: url("../../images/bkg-quote-close.png") no-repeat 0 0 transparent;
      bottom: 0;
      content: "";
      height: 50px;
      position: absolute;
      right: 0;
      width: 65px;
    }
  }
}

.quote {
  margin-bottom: 40px;
  overflow: hidden;

  .image {
    float: right;
    margin-left: 20px;
  }

  .title {
    @include font($size: 21px, $weight: 400, $family: $font-family-sans);
    border-bottom: 2px solid $color-listing-border;
    text-transform: uppercase;
  }

  .quote {
    padding: 0 100px;
    position: relative;
  }

  .inner-quote {
    padding-right: 195px;

    .quote {
      padding: 0 0 0 100px;
    }
  }

  span.open {
    background: url("../../images/bkg-quote-open.png") no-repeat 0 0 transparent;
    content: "";
    display: inline-block;
    height: 51px;
    left: 0;
    position: absolute;
    top: 0;
    width: 65px;
  }

  span.close {
    background: url("../../images/bkg-quote-close.png") no-repeat 0 0 transparent;
    bottom: 0;
    content: "";
    display: inline-block;
    height: 50px;
    position: absolute;
    right: 0;
    width: 65px;
  }
}

.video-wrapper {
  display: inline-block;
  width: 370px;

  a {
    font-family: $font-family-sans;
    margin-bottom: 15px;

    &.video {
      border-bottom: 0px none !important;
      cursor: pointer;
      display: block;
      margin-bottom: 0px;
      margin-top: 20px;
      position: relative;

      &:after {
        background: url("../../images/bkg-play-bttn.png") no-repeat 0 0 transparent;
        content: "";
        height: 60px;
        left: 50%;
        margin-left: -32px;
        margin-top: -30px;
        position: absolute;
        top: 50%;
        width: 64px;
      }

      &:hover {
        border: 0px none;
        opacity: 0.8;
      }

      img {
        height: auto !important;
        max-width: 100%;
      }
    }
  }

  &.right {
    float: right;
    margin: 0 0 25px 25px;
  }
}

.bucket {
  article.node .field-name-body & {
    ul {
      padding: 0;

      li {
        @include bucket-style;
      }
    }
  }
}

article.node {
  img {
    height: auto !important;
    max-width: 100%;
  }

  a {
    border-bottom: 2px solid #cfd2aa;
    color: #20438f;
  }

  a.selectBox {
    border-bottom: 5px solid #3FAD70;
  }
}

.breadcrumb {
  @include font($size: 16px, $line-height: 1.2, $weight: 400, $family: $font-family-sans);
  margin-bottom: 10px;

  li {
    margin-right: 7px;
    display: inline-block;

    &.last {
      span::after {
        content: '';
      }
    }

    span {
      display: block;
      padding-right: 20px;
      position: relative;

      &:after {
        color: #03bdff;
        content: "/";
        font-size: rem-calc(16px);
        position: absolute;
        right: 0;
        top: -3px;
      }
    }

    a {
      color: #20438f;
      padding-right: 20px;
      position: relative;

      &:after {
        color: #03bdff;
        content: "/";
        font-size: rem-calc(16px);
        position: absolute;
        right: 0;
        top: -3px;
      }

      &:hover {
        color: #2f61ce;
      }
    }

    &:first-child {
      position: relative;

      &:after {
        content: "/";
        color: #03bdff;
        font-size: rem-calc(16px);
        position: absolute;
        right: 0;
        top: -3px;
      }

      a {
        color: transparent;
        font-size: 0;
        display: inline-block;
        height: 15px;
        position: relative;
        vertical-align: top;
        width: 30px;

        &:after {
          color: $background-body;
          content: "\f015";
          font-family: $font-awesome;
          font-size: rem-calc(16px);
          left: 0;
          position: absolute;
          top: 0;

          &:hover {
            color: #2f61ce;
          }
        }
      }
    }
  }
}

.field-name-field-social-media,
.view-social-media,
.bean-sidebar-contact,
.social-media-activity-block {
  .views-row {
    border-top: none;
  }

  .field-type-link-field {
