.ui-widget {
  font-family: $font-family-sans;
  font-weight: 700;
}

.ui-datepicker {
  padding: 0;
  width: 100%;

  .ui-datepicker-header {
    margin-bottom: 15px;
  }

  .ui-datepicker-prev,
  .ui-datepicker-next {
    background: none $background-pagination-hover;
    cursor: pointer;
    height: 31px;
    width: 31px;

    &:after {
      color: $color-yellow-accent;
      content: "\f053";
      font-family: $font-awesome;
      font-size: rem-calc(12px);
      left: 50%;
      margin: -14px 0 0 -6px;
      position: absolute;
      top: 50%;
    }

    &:hover {
      border: 0px none;
    }

    span {
      display: none;
    }
  }

  .ui-datepicker-next:after {
    content: "\f054";
    margin: -14px 0 0 -3px;
  }

  .ui-datepicker-prev-hover {
    left: 2px;
    top: 2px;
  }

  .ui-datepicker-next-hover {
    right: 2px;
    top: 2px;
  }

  .ui-widget-header .ui-icon span {
    display: none;
  }

  .ui-datepicker-title {
    @include font($size: 21px, $line-height: 1, $weight: 400, $family: $font-family-sans);

    .ui-datepicker-year {
      display: none;
    }
  }

  table {
    font-size: rem-calc(14px);
    line-height: 14px;

    tr:nth-child(2n) {
      background-color: transparent;
    }
  }

  thead {
    display: none;
  }

  td {
    padding: 0;

    a {
      margin: 5px;
    }

    &:first-child a {
      margin-left: 0px;
    }

    &:last-child a {
      margin-right: 0px;
    }
  }
}

.ui-widget-header {
  background: none transparent;
  border: 0px none;
}

.ui-widget-content {
  background: none #fff;
  border: 0px none;
  color: $color-font;
}

.ui-corner-all,
.ui-corner-bottom,
.ui-corner-right,
.ui-corner-br {
  border-radius: 0px;
}

.ui-state-default,
.ui-widget-content .ui-state-default,
.ui-widget-header .ui-state-default {
  font-family: $font-family-sans;
  font-weight: 700;
  background: none transparent;
  border: 0px none;
  color: #171614;
  padding: 8px 7px;
  text-align: center;

  &:hover {
    background-color: $background-pagination-hover;
    color: #fff;
  }
}

.ui-state-default.ui-state-highlight {
  background-color: $background-pagination-hover;
  color: #fff;
}

