.arts-page #content {
  min-height: 0;
  padding: 0;
  //  display: none;
}

.page-events #main {
  min-height: 1000px;
}

#cboxTitle {
  @include element-invisible;
}

.date-display-listing {
  list-style: none;
  margin: 0;

  li,
  li a {
    border: none;
    color: #827e56;
    font-family: $font-family-sans;
    font-weight: normal;
    font-size: rem-calc(16px);
    line-height: 1.2em;
  }

  a:hover {
    text-decoration: underline;
  }
}

figcaption .group-more-link-wrapper a {
  color: #20438f;
  display: inline-block;
  padding-right: 12px;
  position: relative;

  &:after {
    color: #028abc;
    content: "\f054";
    font-family: "FontAwesome";
    font-size: rem-calc(12px);
    line-height: 12px;
    margin-top: -4px;
    position: absolute;
    right: 0;
    top: 50%;
  }
}

.node-blog.view-mode-full .field-name-field-share {
  margin-bottom: 20px;
  position: relative;
  top: 0px;
}

// Anon event submission form
.not-logged-in.page-node-add-event {
  .form-item-field-featured-und {
    display: none;
  }

  .content-wrapper {
    padding: 30px;
  }

  #edit-field-event-category-und-0-tid-select-1 {
    display: none !important;
  }

  .form-field-name-field-event-contact,
  .form-field-name-field-location {
    .form-autocomplete {
      background: none;
    }

    #autocomplete {
      background: white;
    }
  }

  .form-field-type-datetime {
    .form-wrapper {
      border: none;
    }

    .fieldset-legend {
      font-weight: bold;
    }

    .date-float {
      float: none;
    }

    .date-no-float {
      width: 48%;
      clear: none;

      .form-item-field-event-date-und-0-value2 {
        margin-top: -28px;
      }
    }

    .container-inline-date {
      display: inline-block;
      vertical-align: top;
    }
  }

  form {

    .form-item.form-type-textfield,
    .form-item.form-type-emailfield {
      width: 45%;
    }

    .field-name-title-field,
    .form-field-name-field-caption,
    .form-field-name-field-event-date-note-,
    .form-field-name-field-location,
    .link-field-title.link-field-column {
      display: inline-block;
      margin-right: 3%;
      vertical-align: top;
      width: 45%;

      .form-item {
        width: 100%;
      }
    }

    .form-type-date-popup .form-item.form-type-textfield {
      margin-right: 20px;
    }

    .form-field-name-field-event-audience,
    .form-field-name-field-cost,
    .form-field-name-field-event-contact,
    .form-field-name-field-event-category {
      display: inline-block;
      vertical-align: top;
    }

    .form-field-name-field-cost,
    .form-field-name-field-event-contact {
      margin-right: 3%;
      width: 45%;
    }

    .form-field-name-field-cost label {
      display: inline-block;
    }

    .form-field-name-field-event-dept-category {
      .form-type-checkbox {
        display: inline-block;
        margin-right: 15px;
      }
    }

    .form-field-name-field-sub-category {
      .form-type-checkbox {
        display: inline-block;
        vertical-align: top;
        width: 100%;
      }
    }

    .container-inline-date .date-padding {
      padding: 10px 0px;
    }
  }

  textarea {
    height: 275px;
  }

  #field-event-contact-add-more-wrapper {
    .form-type-textfield {
      width: 100%;
    }
  }
}

.media-colorbox-video {
  display: block;
  position: relative;

  &:after {
    background: url('/sites/all/themes/custom/edu_theme/images/bkg-play-bttn.png') no-repeat 0 0 transparent;
    content: "";
    height: 60px;
    left: 40%;
    position: absolute;
    top: 34%;
    width: 64px;
  }
}

.node-type-person {
  .title-wrapper {
    padding-bottom: 0;
  }

  .view-media-gallery {
    .field-name-field-caption {
      line-height: 1.2em;
      padding: 13px 15px;
    }

    .media-gallery-video {
      .field-name-field-caption {
        background-color: $color-orange-caption;
        margin-bottom: 15px;
        color: #fff;
        line-height: 1.2em;
        padding: 13px 15px;
        margin-bottom: 30px;
      }
    }

    .flex-direction-nav {
      position: relative;
    }
  }

  font-family: $font-family-sans;
  font-size: rem-calc(21px);

  article.node-person {
    min-height: 600px;
  }

  #page {
    padding-bottom: 0;
  }

  .field {
    padding-bottom: 7px;
  }

  span.field-label {
    float: left;
    padding-right: 5px;
    font-style: italic;
    line-height: 1.3;
  }

  .group-office {
    span.field-label {
      font-style: normal;
    }
  }

  .field-name-field-e-mail {
    padding-bottom: 11px;
  }

  .group-office {
    padding-bottom: 4px;
  }

  .group-lab {
    padding-bottom: 4px;
  }

  .field-name-field-year {
    padding-bottom: 10px;
  }

  .field-name-field-position {
    border-bottom: 5px solid #f2efe2;
    font-size: rem-calc(36px);
    margin-bottom: 20px;
    margin-top: 20px;
    padding-bottom: 30px;
    width: 70%;
  }

  .bio-wrapper {
    position: relative;
    padding-bottom: 50px;
  }

  .group-info-section {
    font-size: rem-calc(18px);
    line-height: 1.3;
    margin-top: -130px;
    min-height: 414px;

    .group-info-columns {
      display: block;
      overflow: auto;
      margin-bottom: 20px;
      width: 70%;
      float: left;
      border-bottom: 2px solid #f2efe2;
    }
  }

  .group-first-column,
  .group-second-column {
    margin-top: 130px;
  }

  .group-first-column {
    a {
      border-bottom: 2px solid $color-link-underline;
    }

    width: 48%;
    float: left;

    .field-name-field-primary-address,
    .field-name-field-secondary-address {
      padding-bottom: 0;
    }

    .field-name-field-social-media {
      margin-top: 23px;
      margin-bottom: 10px;

      .field-type-link-field {
        margin-right: 20px;
      }

      a {
        border-bottom: none;

        &:after {
          top: 11px;
          left: 0px;
        }
      }
    }
  }

  .group-second-column {
    width: 48%;
    float: left;

    .group-office-hours {
      span.field-label {
        float: none;
      }

      .field-name-field-office-hours {
        .oh-wrapper {
          .oh-display {
            margin-bottom: 10px;
            width: 50%;
            float: left;

            .oh-display-label {
              display: block;
              line-height: 1.3;
            }
          }
        }
      }
    }
  }

  .group-image {
    float: right;
    position: relative;
    right: 0;
    top: 0;
    width: 24%;

    .field-name-field-image {
      padding-bottom: 0;
      max-height: 380px;

      a {
        border-bottom: none;

        img {
          display: block;
        }
      }
    }

    .field-name-view-larger-image {
      @include green-button-link;
      background: $color-border-dark-green;

      a {
        @include font($size: 14px, $line-height: 2, $weight: 700, $family: $font-family-sans);
        border-bottom: none;
        color: #fff;
        display: block;
        letter-spacing: 1px;
        padding: 3px 10px 3px 12px;
        text-transform: uppercase;

        &:after {
          content: "\f019";
          font-size: rem-calc(14px);
          padding-left: 20px;
        }
      }
    }
  }

  .group-info-bottom {
    overflow: auto;
    padding-bottom: 20px;
    width: 70%;
    float: left;

    .field-name-field-download-cv-as-pdf {
      @include green-button-link;
      font-family: $font-family-sans;
      float: left;
      padding-right: 70px;

      a {
        background: $color-border-dark-green;
        font-size: rem-calc(14px);
        letter-spacing: 1px;
        padding: 7px 10px 7px 14px;

        &:after {
          content: "\f019";
          font-size: rem-calc(14px);
        }
      }

      .file-icon,
      .file-size {
        display: none;
      }
    }

    .field-name-field-external-cv {
      @include green-button-link;
      font-family: $font-family-sans;
      font-weight: 700;
      float: left;
      padding-right: 70px;

      a {
        background: $color-border-dark-green;
        font-size: rem-calc(14px);
        letter-spacing: 1px;
        padding: 7px 10px 7px 14px;

        &:after {
          content: "\f019";
          font-size: rem-calc(14px);
        }
      }
    }

    .field-name-field-show-media-relations-block {
      float: left;
      color: #827e56;
      line-height: 1.3;
      font-size: rem-calc(18px);

      a {
        border-bottom: 2px solid $color-link-underline;
      }
    }
  }

  .group-quicktabs {
    margin-left: -50px;
    margin-right: -50px;
    float: left;
    margin-top: 110px;
    width: 100%;

    .horizontal-tabs ul.horizontal-tabs-list {
      background-color: transparent;
      border-right: none;
    }

    // Left justify a single tab.
    &.group-quicktabs-single .horizontal-tabs .horizontal-tabs-list {
      margin-left: 50px;
      text-align: left;
    }

    .horizontal-tabs {
      border: none;
      margin-bottom: 0;

      .horizontal-tab-button {
        font-family: $font-family-sans;
font-weight: 700;
        font-weight: normal;

        strong {
          font-weight: normal;
        }
      }

      .horizontal-tabs-list {
        text-transform: uppercase;
        text-align: center;

        li.selected {
          background-color: $color-link-listed;

          strong {
            color: #fff;
          }

          a {
            color: #fff;
            font-size: rem-calc(21px);
            padding: 0.6em 0.9em 0.6em 0.9em;

            &:hover {
              background-color: $color-link-listed;
            }
          }
        }

        li {
          display: inline-block;
          float: none;
          margin-right: 10px;
          border-right: none;
          background-color: $color-border-bucket-blue;
          margin-bottom: -5px;

          &:hover {
            background-color: $color-link;
          }

          a {
            border-bottom: none;
            color: #fff;
            padding: 0.6em 0.9em 0.4em 0.9em;

            &:hover {
              background-color: $color-link;
            }
          }
        }
      }
    }

    .horizontal-tabs-panes {

      // border-top: 5px solid $color-link;
      h3 {
        font-weight: bold;
        margin: 0 0 23px 0;
      }
    }

    .horizontal-tabs-pane {
      background: url(/sites/all/themes/custom/edu_theme/images/bkg-pttrn-lt-grey.png) repeat 0 0 transparent;
      padding: 38px 50px 50px 50px;
      line-height: 1.5;
      @include bullet-list-style;

      li {
        list-style-position: outside;
      }

      a {
        border-bottom: 2px solid $color-link-underline;
      }
    }

    .group-expertise {
      .field-name-field-expertise {
        font-family: $font-family-serif;
        display: table-cell;
        min-width: 75%;
        //float: left;
        padding-right: 5%;
        line-height: 1.3;

        @media(max-width: 767px) {
          display: block;
          width: 100%;
        }
      }

      .field-field-person-areas-of-expertise-wrapper {
        display: table-cell;
        width: 25%;
        //float: right;
        margin-top: 10px;
        padding-right: 25px;

        @media(max-width: 767px) {
          display: block;
          width: 100%;
        }
      }

      .field-name-field-person-areas-of-expertise {
        p {
          margin-bottom: 10px;
          text-transform: uppercase;
        }

        ul {
          margin-top: 0;
        }
      }

      .group-research-interests {
        width: 70%;
        float: left;
        padding-right: 5%;
        font-family: $font-family-serif;
        background: #fff;
        padding: 0 20px;
        margin-bottom: 15px;
        border-top: 5px solid #ccc;

        h3 {
          font-family: $font-family-sans;
          text-transform: uppercase;
          font-size: rem-calc(18px);
          font-style: normal;
          color: #000;
        }
      }
    }

    .publishing-settings {
      .field-name-field-other-information {
        display: table-cell;
        min-width: 75%;
        padding-right: 5%;

        @media(max-width: 767px) {
          display: block;
          width: 100%;
        }
      }

      .group-aside {
        display: table-cell;
        width: 25%;

        @media(max-width: 767px) {
          display: block;
          width: 100%;
        }
      }
    }

    .group-work {
      .group-featured-publications {
        font-family: $font-family-serif;
        line-height: 1.3;
        width: 70%;
        float: left;
        display: block;
      }

      .group-featured-work {
        width: 25%;
        float: right;
        display: block;

        .field-name-view-in-gallery {
          background-color: $color-border-dark-green;
          overflow: auto;
          @include green-button-link;

          a {
            font-family: $font-family-sans;
font-weight: 700;

            &:after {
              content: "\f00a";
              float: right;
              padding: 4px 16px;
              font-size: rem-calc(17px);
            }
          }
        }

        img {
          max-width: 100%;
          height: auto;
        }
      }

      .featured-work-gallery {
        .views-row {
          background-color: transparent;
          border-top: none;

          .field-name-field-media {
            padding: 0;
          }

          img {
            display: block;
            height: auto;
            width: 100%;

          }
        }
      }
    }

    .group-education {
      font-family: $font-family-serif;

      .group-education-left {
        width: 70%;
        float: left;
        display: block;

        .group-prof-experience {
          h3 {
            padding-top: 10px;
          }
        }

        .field-name-field-degrees {
          .country {
            display: none;
          }

          .field {
            margin-bottom: 0;
            padding-bottom: 0;
          }

          .field-name-field-year {
            float: left;

            &:after {
              content: ",";
              margin-left: -4px;
              margin-right: 5px;
            }
          }
        }
      }

      .group-education-right {
        width: 25%;
        float: right;
        display: block;

        .group-recent-courses {
          background: #fff;
          border-top: 5px solid #ccc;
          padding: 0 20px;

          .field-label {
            font-family: $font-family-sans;
            color: #000;
            font-size: rem-calc(19px);
            font-style: normal;
            text-transform: uppercase;
          }
        }
      }
    }

    .group-awards-honors {
      .group-awards-honors-body {
        font-family: $font-family-serif;
        line-height: 1.3;

        h3 {
          padding-bottom: 20px;
        }

        table {
          @include table-style-two;
          border: 2px solid #fff;
        }
      }
    }

    .group-more-info {
      .field-name-field-other-information {
        padding: 15px;
      }
    }

    // additional tabs
    .publishing-settings {
      font-family: $font-family-serif;

      .additional_tabs_block {
        .field-name-field-title {
          padding: 0;
        }
      }
    }
  }
}

// Dining Hall Pages
.section-dining-services,
.page-node-43,
.page-node-44,
.page-node-45 {
  .sidebar-menu-block {
    margin-top: -5px;

    .block-title {
      color: $color-link;
      border-bottom: 2px solid $color-block-title-underline;
      padding-bottom: 10px;
    }

    ul.menu {
      li {
        background: $color-link !important;
        margin-bottom: 3px !important;

        a {
          &:after {
            display: none;
          }
        }
      }

      li.first {
        background: $color-link-listed !important;
      }
    }
  }

  .menu_week {
    text-align: center;
    margin: 70px 0;
  }

  .table-caption {
    text-transform: uppercase;
  }

  .region-sidebar-second {
    padding-left: 15px;
  }

  .horizontal-tabbed-menu {
    margin-left: -20px;
    margin-right: -20px;
  }

  .dining-hall-hours-block {
    font-family: $font-family-sans;
    border-top: 6px solid $color-listing-border;
    padding: 20px 5px 15px 15px;
    background: #fff;

    .dining-period {
      color: $color-form-text;
    }

    .dining-days-col-1,
    .dining-days-col-2,
    .dining-days-col-3 {
      width: 32%;
      float: left;
      display: block;
      font-size: rem-calc(16px);

      span {
        color: $color-form-text;
      }
    }

    .dining-days-col-2 {
      width: 37%;
    }

    .dining-days-col-3 {
      float: right;
      width: 26%;
    }

    .dining-days {
      font-size: rem-calc(21px);
    }

    .dining-hall-location {
      display: block;
      float: left;
      color: $color-form-text;
      font-style: italic;
      margin-top: 20px;
    }
  }

  #content {
    background: url(/sites/all/themes/custom/edu_theme/images/bkg-pttrn-lt-grey.png) repeat 0 0 transparent;
    padding: 20px;
    padding-top: 0;
    width: 72.6%;
  }

  #sub-nav li ul,
  ul.collapse li ul,
  #left-nav li ul,
  .js_hidden_on_start {
    display: none;
  }

  table.menu {
    margin-bottom: 20px !important;
    display: none;

    th {
      padding: 10px 15px 15px;
      font-size: 110%;
      text-align: center;
    }

    td {
      padding: 15px;
    }

    th.station {
      background: #afd3ea;
      border: 2px solid #fff;
      color: #5f5f5c;
    }

    td.station {
      font-size: 110%;
      width: 80px;
      background: #fff;
      border: 1px solid #fff;
      color: #6c6c6a;
      text-shadow: 0 1px 0 #f0f0eb;
      font-weight: 700;
    }

    td.breakfast,
    table.menu td.brunch,
    table.menu td.lunch,
    table.menu td.dinner {
      width: 200px;
    }

    th.breakfast {
      background: #e8d4e9;
      border: 2px solid #fff;
      color: #727466;
      text-shadow: 0 1px 0 #faffe0;
    }

    td.breakfast {
      background: #f7eff8;
      border: 2px solid #fff;
      color: #62635e;
    }

    th.brunch {
      background: #ffe0a2;
      border: 2px solid #fff;
      color: #807a70;
    }

    td.brunch {
      background: #fff6e3;
      border: 2px solid #fff;
      color: #65625e;
    }

    th.lunch {
      background: #ffe0a2;
      border: 2px solid #fff;
      color: #656d71;
    }

    td.lunch {
      background: #fff6e3;
      border: 2px solid #fff;
      color: #5e6163;
    }

    th.dinner {
      background: #b3e0c3;
      border: 2px solid #fff;
      color: #636e68;
    }

    td.dinner {
      background: #e5f5e5;
      border: 2px solid #fff;
      color: #606361;
    }

    span.mealName {
      padding: 4px 8px 4px 6px;
      margin: 10px 0 0;
      font-weight: 400;
      font-size: 95%;
      display: block;
      text-align: center;
    }

    th.breakfast span.mealName {
      background: #cdd0b7;
    }

    th.lunch span.mealName {
      background: #b6c5cd;
    }

    th.dinner span.mealName {
      background: #b2c6bc;
    }
  }

  table.short td.brunch,
  table.short td.dinner {
    width: 257px;
  }

  div.table-caption {
    font-family: $font-family-sans;
    cursor: pointer;
    height: 40px;
    line-height: 30px;
    margin-bottom: 10px;
    background: #018abb;
    color: #fff;
    padding: 6px 10px;
    font-size: 90%;

    span {
      display: block;
      float: right;
      width: 15px;
      height: 12px;
      margin: 0 0;
      color: $color-chevron;
      font-size: rem-calc(30px);
      font-weight: bold;
      margin-top: -4px;

      &:after {
        content: "-";
      }
    }
  }

  #menus div.hide {
    opacity: .6;
    display: block !important;

    span {
      background-position: -16px 0;
      width: 18px;
      height: 12px;
      color: $color-chevron;
      font-size: rem-calc(30px);
      font-weight: bold;
      margin-top: -1px;

      &:after {
        content: "+";
      }
    }
  }

}

