.node-type-event {
  .group-location {
    .field-label {
      float: left;
      padding-right: 3px;
    }
  }
}

.node-type-article {
  @at-root .not-front.not-logged-in & {
    #page {
      padding-bottom: 110px;
    }
  }
}

.node-type-event {
  @at-root .not-front.not-logged-in & {
    #page {
      padding-bottom: 85px;
    }
  }

  .field-name-field-off-campus-location {
    margin-top: 30px;
    margin-bottom: 30px;

    p {
      margin-bottom: 0;
    }

    &:after {
      color: #ffe121;
      content: "\f041";
      font-family: "FontAwesome";
      font-size: rem-calc(24px);
      left: 0;
      position: absolute;
      top: 0;
    }
  }

  .field-name-field-directions-parking {
    a {
      padding: 5px 37px 5px 12px !important;
    }
  }

  .field-name-field-off-campus-directions-and- {
    a {
      font-family: 'futurapt_heavy', Arial, Verdana, Tahoma, sans-serif;
      font-weight: normal;
      font-size: rem-calc(14px);
      line-height: 1.6em;
      background-color: #af74b1;
      color: #fff;
      display: inline-block;
      letter-spacing: 1px;
      padding: 5px 37px 5px 12px;
      position: relative;
      text-transform: uppercase;

      &:after {
        color: #ffe121;
        content: "\f054";
        font-family: "FontAwesome";
        font-size: rem-calc(12px);
        line-height: 12px;
        margin-top: -6px;
        position: absolute;
        right: 13px;
        top: 50%;
      }
    }
  }

  .node-event.view-mode-full .group-body-wrapper {
    border-top: 0px none;
    padding-left: 0;
    max-width: 100%;
  }

  &.has-banner {
    .node-event.view-mode-full .group-body-wrapper {
      border-top: 6px solid $color-link-underline;
      padding-left: 37px;
      max-width: 975px;
    }

    .field-name-field-share {
      margin-bottom: 0px;
      position: absolute;
      top: -9px;
    }
  }

  .field-name-field-share {
    margin-bottom: 13px;
    position: relative;
    top: 0;
  }

  .group-bio-information {
    display: block;
    overflow: auto;
  }

  .group-bio-left {
    width: 48%;
    margin-right: 2%;
    float: left;

    .field-name-field-details {
      font-size: rem-calc(18px);

      a {
        font-size: rem-calc(18px);
        font-family: $font-family-sans;

        &:after {
          font-family: $font-awesome;
          font-size: rem-calc(12px);
          content: " \f054";
        }
      }
    }
  }

  .group-bio-right {
    width: 50%;
    float: left;

    .field-name-field-caption {
      color: $color-link;
      font-family: $font-family-sans;
      font-size: rem-calc(18px);

      span {
        border-bottom: 2px solid $color-link-underline;
      }
    }

    .file-video-youtube {
      margin-top: 20px;

      &:after {
        background: url(/sites/all/themes/custom/edu_theme/images/bkg-play-bttn.png) no-repeat 0 0 transparent;
        content: "";
        height: 60px;
        left: 50%;
        margin-left: -32px;
        margin-top: -30px;
        position: absolute;
        top: 50%;
        width: 64px;
      }
    }

    .field-name-field-media {
      img {
        margin-bottom: -40px;
        margin-top: -31px;
        width: 100%;
      }
    }
  }
}

//jp-audio
div.jp-audio {
  width: 100%;
  background: url(/sites/all/themes/custom/edu_theme/images/bkg-pttrn-lt-grey.png) repeat 0 0 transparent;
  padding: 15px;
  clear: both;

  .jp-interface {
    border: none;
    background: #fff;

    .jp-controls li {
      background: none;

      .jp-play {
        width: 28px;
        height: 28px;
        background-size: 138px;
        top: 26px;
        left: 54px;

        &:hover {
          background-position: -28.2px 0;
        }
      }
    }
  }

  .jp-playlist {
    margin-top: 15px;
    border: none;
    background: #fff;

    ul {
      li {
        list-style-type: decimal;
        background: none;
        list-style: decimal;
        overflow: initial;
        list-style-position: inside;

        &.jp-playlist-last {
          padding: 5px 15px;

        }
      }
    }
  }

  div.jp-type-playlist div.jp-playlist li.jp-playlist-current {
    list-style-type: decimal;
    padding-left: 15px;
  }

  article.node .field-name-body ul li {
    background: none;
  }

  /*
  article.node .field-name-body ul li, .bullet-list-style ul li {
    background: none;
    list-style: decimal;
    overflow: auto;
  }
*/
}

.node-article.view-mode-full,
.page-node {
  @include article-event-styles;
  position: relative;

  .group-body-wrapper {
    padding: 15px 40px 0;
    max-width: 975px;
  }

  .field-name-field-event-contact {
    padding-bottom: 20px;
  }


  .field-name-body {

    // Stack Table info
    table.small-only {
      display: none;
    }

    p.first-paragraph:first-letter,
    p.first-paragraph::first-letter,
    span.first-letter {
      color: #20438f;
      float: left;
      font-size: rem-calc(74px);
      font-weight: bold;
      padding-right: 2px;
      padding-top: 14px;
    }

    .group-figure {

      .group-caption,
      .required-fields {
        font-family: $font-family-sans;
        font-size: rem-calc(20px);

        a {
          &:after {
            font-family: $font-awesome;
            content: "\f054";
            font-size: rem-calc(14px);
            padding-left: 7px;
          }
        }
      }
    }

    figcaption {
      border-bottom: 2px solid #f2efe2;
      font-size: .8em;
      font-family: $font-family-sans;
      line-height: 1.3em;
      padding-bottom: 20px;
    }

    .left,
    .right {
      display: inline-block;
      margin: 0;
      max-width: 369px;
      padding-bottom: 10px;

      &.inset-audio {
        .jp-interface {
          height: 140px;

          .jp-mute {
            top: 80px;
            left: 200px;
          }

          .jp-progress {
            top: 80px;
            left: 20px;
          }

          .jp-volume-bar {
            top: 86px;
            left: 230px;
          }

          .jp-current-time {
            top: 100px;
            left: 20px;
          }

          .jp-duration {
            top: 100px;
            left: 20px;
          }
        }
      }

      img {
        display: block;
        margin-bottom: 15px;
        min-width: 359px;
      }
    }

    .full {
      img {
        width: 100%;
      }
    }

    .left {
      float: left;
      padding: 10px 10px 10px 0;
      margin-left: -120px;
      margin-right: 20px;
      clear: both;

      a {
        border-bottom: none !important;
      }
    }

    .inset {
      p {
        font-size: .8em;
        font-family: 'futurapt_book', Arial, Verdana, Tahoma, sans-serif;
        line-height: 1.3em;
      }
    }

    .right {
      float: right;
      padding: 10px 0 10px 10px;
      margin-left: 20px;
      margin-right: -120px;
      clear: both;

      a {
        border-bottom: none !important;
      }
    }

    .image-caption {
      font-size: rem-calc(16px);
      font-family: $font-family-sans;
      line-height: 1.2em;
    }

    .no-caption {
      display: none;
    }
  }

  .field-name-field-body-media {
    border-bottom: 2px solid #f2efe2;
    float: left;
    margin: 0 25px 25px -75px;
    max-width: 369px;
    padding-bottom: 10px;

    .content {
      margin-bottom: 15px;

      img {
        display: block;
      }
    }

    .field-type-text {
      font-size: rem-calc(16px);
      font-family: $font-family-sans;
      line-height: 1.2em;
    }

    &.right {
      float: right;
      margin: 0 -75px 25px 25px;
    }
  }

  .group-article-footer {
    @include node-info-style;
    font-size: rem-calc(18px);
    border-top: 2px solid #f2efe2;
    clear: both;
    line-height: 1.3em;
    padding-top: 15px;

    span.field-label {
      float: left;
      padding-right: 5px;
    }

    a {
      @include list-link-style;
    }
  }
}

.node-type-page,
.node-type-event {
  .right {
    margin-right: 10px !important;
  }

  .left {
    margin-left: 10px !important;
  }
}

.node-blog.node-teaser {
  @include list-view-content-styles;
  @include listing-view-style;

  .field-name-title-field {
    h4 {
      margin: 0;
      padding: 0;
    }
  }

  .field-name-title-field,
  .field-name-field-publish-date,
  .field-name-body {
    margin-left: 275px;
  }
}

.node-article.node-teaser {
  @include list-view-content-styles;
  @include listing-view-style;

  .links.inline {
    display: block;

    .node-readmore a {
      @include icon-button;
    }
  }

  .field-name-title-field,
  .field-name-field-publish-date,
  .field-name-body,
  .links.inline {
    margin-left: 275px;
  }
}

.node-event.view-mode-full,
.node-blog.view-mode-full {
  @include article-event-styles;
  position: relative;

  .group-info {
    margin-bottom: 10px;
  }

  .group-body-wrapper {
    overflow: hidden;
    padding: 15px 0px 0 37px;
  }

  .group-left-wrapper {
    display: inline-block;
    margin-right: 40px;
    margin-right: 3.1%;
    vertical-align: top;
    width: 68.7%;
  }

  .field-name-body {
    margin-bottom: 20px;
    overflow: hidden;
    padding-bottom: 20px;
  }

  .group-event-details {
    font-family: $font-family-sans;
    float: right;
    vertical-align: top;
    width: 27.4%;

    .view-mode-listing .field-name-title-field,
    .view-mode-contact .field-name-title-field {
      margin: 0;
      padding: 0;
    }

    .field-group-div {
      margin-bottom: 35px;
      padding-left: 30px;
      position: relative;
    }
  }

  .group-event-date {

    &:after {
      color: $color-yellow-accent;
      content: "\f017";
      font-family: "FontAwesome";
      font-size: rem-calc(24px);
      left: 0;
      position: absolute;
      top: 0;
    }

    .field-name-field-event-date {
      padding-right: 40px;
    }
  }

  .group-location {

    .item-list {
      ul {
        list-style: none;

        li {
          margin: 0;
        }
      }
    }

    &:after {
      color: $color-yellow-accent;
      content: "\f041";
      font-family: "FontAwesome";
      font-size: rem-calc(24px);
      left: 0;
      position: absolute;
      top: 0;
    }
  }

  .group-additional-info {
    &:after {
      color: $color-yellow-accent;
      content: "\f145";
      font-family: "FontAwesome";
      font-size: rem-calc(24px);
      left: 0;
      position: absolute;
      top: 0;
    }

    .field-name-field-cost,
    .field-name-field-event-audience {
      display: inline-block;
      vertical-align: top;
    }
  }

  .field-name-field-add-to-calendar,
  .field-name-field-directions-parking,
  .field-name-field-sign-me-up-link {
    a {
      @include icon-button;
    }
  }

  .field-name-field-add-to-calendar,
  .field-name-field-directions-parking {
    padding-top: 20px;
  }

  .group-contact-wrapper {
    font-family: $font-family-sans;
    font-size: rem-calc(18px);
    color: $color-form-text;
    line-height: 1.4em;
    border-top: 2px solid #f2efe2;
    padding-top: 25px;

    h3.field-label {
      font-family: $font-family-sans;
      font-size: rem-calc(18px);
      color: $color-form-text;
      font-style: normal;
      line-height: 1.1em;
      margin: 0;
    }

    .view-mode-listing .field-name-title-field,
    .view-mode-contact .field-name-title-field {
      margin: 0 0 5px;
      padding: 0;

      a {
        border-bottom: 2px solid #cfd2aa;

        &:hover {
          border-bottom-color: transparent;
        }
      }
    }

    .field-name-field-title-position {
      font-style: italic;
    }

    .field-name-field-phone {
      line-height: 1.1em;
    }
  }

  .addtocal {
    cursor: pointer;
  }

  ul.addtocal_menu {
    border-radius: 0;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border: none;
    padding: 0;
    list-style-type: none;
    margin-left: 0px !important;
    margin-bottom: 0px;
    position: absolute;
    display: none;
    min-width: 0;
    z-index: 50;
    width: 184px;
    margin-top: 5px;
    background: #fff;
  }

  ul.addtocal_menu li {
    margin: 0px;
    padding: 0px;
  }

  ul.addtocal_menu li a {
    font-family: $font-family-sans;
font-weight: 700;
    background: #af74b1;
    color: #fff !important;
    border-bottom: 4px solid #fff;
    color: inherit;
    text-decoration: none;
    display: block;
    padding: 5px;
    letter-spacing: 1px;
    font-size: rem-calc(15px);
    padding-left: 15px;
  }

  ul.addtocal_menu li a:after {
    color: #ffe121;
    content: "\f054";
    font-family: "FontAwesome";
    font-size: rem-calc(14px);
    line-height: 14px;
    margin-top: 4px;
    right: 9px;
    top: 50%;
    float: right;
    padding-right: 3px;

  }

  ul.addtocal_menu li a:hover {
    text-decoration: none;
    opacity: .8;
    border-radius: 0;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
  }

  ul.addtocal_menu li:last-child {
    a {
      border-bottom: none;
    }
  }
}

