  }
}

.people-directory-page {
  .directory-menu-block {
    margin-bottom: 0px;
  }
}

@media (max-width: 1440px) {

  .node-article.view-mode-full .field-name-body .right,
  .page-node .field-name-body .right {
    margin-right: 0;
  }

  .node-article.view-mode-full .field-name-body .left,
  .page-node .field-name-body .left {
    margin-left: 0;
  }
}

@media (max-width: 1270px) {
  .node-type-person .group-quicktabs {
    margin: 2px;
  }
}

@media (max-width: 1150px) {
  .node-article.view-mode-full {

    .field-name-body blockquote {
      &:before {
        left: -15px;
      }

      &:after {
        right: -15px;
      }
    }
  }
}

@media (max-width: 1030px) {
  .sidebar-first.two-sections-landing-page .region-sidebar-first {
    background-color: $color-link-listed;
    padding: 0;
  }
}

@media (max-width: 959px) {
  .not-logged-in.page-node-add-event form .form-field-name-field-sub-category .form-type-checkbox {
    width: 45%;
  }

  .node-event.view-mode-full,
  .node-article.view-mode-full {
    padding-bottom: 0px;
  }

  .node-event {

    .group-bio-information,
    .group-bio-left,
    .group-bio-right {
      width: 95%;
      margin: auto;
    }

    .group-contact-wrapper {
      margin-top: 25px;
    }
  }
}




