    .arts-intro-block,
    #block-edu-custom-slideshow-sidebar {
      width: 100%;
      height: auto;
      background: url(/sites/all/themes/custom/edu_theme/images/bkg-arts-intro-block.jpg) no-repeat 0 0 $color-link-listed;

      ul.menu li,
      ul.links li {
        display: inline-block;
        padding-right: 10px;
      }
    }

