          height: auto;
        }
      }

      .slide-caption-wrapper {
        max-width: 92%;
        position: relative;
        top: -155px;

      }

      .slide-caption-inner-wrapper {
        position: relative;
        left: 0 !important;
        line-height: 1;
      }

      .flexslider {
        .flex-direction-nav {

          .flex-prev,
          .flex-next {
            top: 27.5%;
          }
        }

        .flex-control-paging {
          bottom: 115px;
        }
      }
    }
  }

  .highlighted-slideshow-block {
    .slide-container-detailed_slide {
      .slide-caption-wrapper {
        .field-name-field-caption-title {
          font-size: rem-calc(40px);
        }

        .field-name-field-caption {
          font-size: rem-calc(20px);
          line-height: 1.3;
        }
      }
    }
  }

  .bottom-wrapper {

    //     background: none;
    &:after {
      background: none;
    }

    .footer-contact-info {
      width: 50%;
      padding-left: 20px;
    }

    .footer-menu-block {
      width: 49%;
      float: left;
      column-count: 1;
      -moz-column-count: 1;
      -webkit-column-count: 1;
      padding-top: 0;

      .menu-name-menu-footer-menu {
        padding-top: 20px;
      }

      li {
        width: 50%;
        float: left;
      }
    }

    .footer-testimonials {
      max-width: 100%;
      width: 100%;
      border-top: 2px solid $color-footer-border;
      border-left: none;
      margin-top: 20px;
