      }

      .jp-progress {
        top: 80px;
        left: 20px;
      }

      .jp-volume-bar {
        top: 86px;
        left: 230px;
      }

      .jp-current-time {
        top: 100px;
        left: 20px;
      }

      .jp-duration {
        top: 100px;
        left: 20px;
      }
    }
  }


