      top: 35%;
      background-size: cover;
      width: 80px;
      height: 80px;
    }
  }

  .slide-container-detailed_slide .slide-caption-wrapper {
    padding: 35px 45px 35px;
    position: relative;
    height: 250px;

    .slide-caption {
      float: none;
      width: 100%;

      .field-name-field-caption-title {
