@import 'headers';
@import 'links';
@import 'lists';

@mixin font($size: 1em, $style: null, $line-height: null, $family: 'sans-serif', $weight: null) {
  @if $line-height != '' {
    $font-size: #{rem-calc($size)}/#{$line-height};
  } @else {
    $font-size: rem-calc($size);
  }
  font: $style $weight $size $family;
}
