.page-search {

  // Padding surrounding content
  $search-page-padding: 2.903553299492%;

  #search-form,
  .gsc-orderby {
    width: 50%;

    form .form-submit,
    form input[type='submit'] {
      margin-top: 25px;
    }
  }

  .gsc-above-wrapper-area {
    padding: 5px 0 6px 0;
  }

  .gsc-webResult .gsc-result {
    padding: 0.4em 0 1.3em 0;
    ;
  }

  .gsc-webResult .gsc-result,
  tbody {
    border: none;
  }

  .gsc-wrapper a {
    color: #02457a;
  }

  .gs-webResult div.gs-visibleUrl-short {
    display: none;
  }

  .gs-webResult div.gs-visibleUrl-long {
    display: block;
  }

  .gs-webResult div.gs-visibleUrl,
  .gs-imageResult div.gs-visibleUrl {
    color: #1f2122;
  }

  .gs-web-image-box img.gs-image,
  .gs-promotion-image-box img.gs-promotion-image {
    border: 1px solid #e2e2e2;
    max-width: 60px;
    max-height: 120px;
  }



  .gsc-table-cell-thumbnail,
  .gs-promotion-image-cell {
    padding-right: 10px;
  }

  .gsc-results .gsc-cursor-box {
    color: #23313e;

    .gsc-cursor-current-page {
      color: #233141;
    }

    .gsc-cursor-page {
      color: #4d7ea9;
    }
  }

  .search-results {
    .search-result {
      margin-bottom: 35px;
    }

    h3.title {
      font-size: rem-calc(21px);
      .futura-book {
        font-style: normal;
        margin-bottom: 15px;  
      }

      a {
        color: $color-link-listed;
          border-bottom: 2px solid $color-link-underline;
      }
    }

    .search-snippet-info {
      font-size: rem-calc(18px);
      line-height: 1.2em;
      padding-left: 0;

      .search-info {
        display: none;
      }
    }
  }

  ul.pager {
    float: none;
    width: 100%,
  }
}








//***************************************************page-program.css.scss copy due to ie9 bug not reading 31th css files********************************************************************//

.node-type-program {
  .content-inner-wrapper {
    max-width: 100%,
  }
}

.node-program {

  // Because group research slideshow has text inside the slideshow it has to be themed specifically for this slideshow
  .group-research-content {
    .field-name-field-research-slideshow {
      ol.flex-control-paging {

        left: 0;
        text-align: center;
        top: 100%;
        width: 100%;
        @media (min-width: 1080px) {
          width: auto;
          left: 25%;
        }
      }

      .media-colorbox-video {

        // Setting media ranges because the play button is an image and not an icon.
        &:after {
          @media (min-width: 768px) and (max-width: 959px) {
            left: 46%;
            top: 42%;
          }
        }
      }
    }
  }

  .media-colorbox-video {

    // Setting media ranges because the play button is an image and not an icon.
    &:after {
      left: 44%;

      // tablet
      @media (max-width: 959px) {
        left: 40%;
        top: 28%;
      }

      @media (max-width: 768px) {
        left: 45%;
        top: 40%;
      }

      @media (max-width: 500px) {
        left: 41%;
        top: 32%;
      }

      @media (max-width: 400px) {
        left: 38%;
      }
    }
  }

  .field-name-field-body-slideshow {
    ol.flex-control-paging {
      left: 0;
      text-align: center;
      width: 100%;

      @media (max-width: 768px) {
        width: 100%;
        text-align: center;
        left: 0;
      }
    }
  }

  &.view-mode-full {

    .quickedit-form {
      .group-study {
        background: none;
        color: $color-font;
        padding-bottom: 10px;

        ul li {
          background: none;
          color: $color-font;

          a:visited,
          a:link {
            border-bottom: 0px none;
            color: $color-link;
          }
        }

        .field-name-field-study {
          width: 100%;
        }

        .field-name-field-study-links {
          width: 100%;
        }

        .field-name-field-research-slideshow .field-name-field-media {
          width: 100%;
        }
      }
    }

    .group-top-row,
    .group-related-programs {
      padding: 0 75px 20px;
    }

    .program-section {
      border-top: 6px solid #0189BB;
      margin-top: 0px;
      padding-left: 75px;
      padding-right: 75px;
      padding-top: 50px;
      position: relative;

      >h3 {
        color: #FFF;
        font-size: rem-calc(36px);
        font-style: normal;
        margin: 0;
        position: absolute;
        top: -0.75em;
        left: 75px;

        span {
          background: none #0189BB;
          padding: 2px 13px;
        }
      }

      ul.field-type-link-field {
        @include sidebar-menu-link-style;

        li a {
          border-bottom: 0px none !important;
        }
      }
    }

    .group-body.program-content-width {
      width: 64.95%;
    }

    .group-body.no-slideshow {
      width: 100%;
      padding-right: 0;
    }

    .program-content-width {
      @include inline-block;
      width: 74%;

      +.field {
        @include inline-block;
        margin-top: 0;
        padding-right: 0;
        width: 25%;
      }

      +.field-name-field-body-slideshow {
        padding-right: 0px;
        width: 34%;
      }
    }

    .field-name-field-subheader {
      font-family: $font-family-sans;
      font-size: rem-calc(36px);
      border-bottom: 6px solid #F1EFE1;
      line-height: 1.3em;
      padding-bottom: 1em;
      margin-bottom: 1em;

      p {
        margin-bottom: 0px;
      }
    }

    .group-study {
      &.study-bike {
        background: #1D468D;
      }

      &.study-bicycle {
        background: url("../../images/bkg-major-undergrad-research.jpg") no-repeat 0 0 #1D468D;
      }

      &.study-circles {
        background: url("../../images/bkg-major-undergrad-research-circle.jpg") no-repeat 0 0 #1D468D;
      }

      &.study-beaker {
        background: url("../../images/bkg-major-undergrad-research-beaker.jpg") no-repeat 0 0 #1D468D;
      }

      &.study-book {
        background: url("../../images/bkg-major-undergrad-research-book.jpg") no-repeat 0 0 #1D468D;
      }

      &.study-mask {
        background: url("../../images/bkg-major-undergrad-research-mask.jpg") no-repeat 0 0 #1D468D;
      }

      &.study-brush {
        background: url("../../images/bkg-major-undergrad-research-brush.jpg") no-repeat 0 0 #1D468D;
      }

      &.study-atom {
        background: url("../../images/bkg-major-atom.jpg") no-repeat 0 0 #1D468D;
      }

      &.study-binary {
        background: url("../../images/bkg-major-binary.jpg") no-repeat 0 0 #1D468D;
      }

      &.study-brain {
        background: url("../../images/bkg-major-brain.jpg") no-repeat 0 0 #1D468D;
      }

      &.study-earth {
        background: url("../../images/bkg-major-earth.jpg") no-repeat 0 0 #1D468D;
      }

      &.study-pi {
        background: url("../../images/bkg-major-pi.jpg") no-repeat 0 0 #1D468D;
      }

      &.study-music-note {
        background: url("../../images/bkg-major-undergrad-research-musicnote.jpg") no-repeat 0 0 #1D468D;
      }

      background: #1D468D;
      color: #FFF;
      padding-bottom: 60px;
      @include bullet-list-style;

      ul {
        li {
          background-image: url("../../images/bkg-bullet-blue-bkg.jpg");

          a {

            &:visited,
            &:link {
              border-bottom: 2px solid $color-link;
              color: #fff;
            }
          }
        }
      }

      .field-name-field-study {
        padding-right: 2%;
        width: 38%;
      }

      .group-fact {
        padding-right: 3%;
        width: 34.5%;
      }

      .field-name-field-study-links {
        padding-right: 0;
        width: 25%;
      }
    }

    .field-name-field-study,
    .group-fact,
    .field-name-field-study-links {
      @include inline-block;
      width: 33%;
    }

    .field-name-field-fact-number {
      font-family: $font-family-sans;
      border-bottom: 6px solid #FFE121;
      font-size: 100px;
      line-height: 1.1em;
      margin-bottom: 20px;

      span {
        font-size: 80%;
        top: -0.2em;
        position: relative;
      }
    }

    .field-name-field-fact-detail {
      font-family: $font-family-sans;
      font-size: rem-calc(24px);
      line-height: 1.2em;
    }

    .group-research {
      @include background-pattern;
      margin-top: 0px;
      padding-bottom: 45px;
    }

    .field-name-field-research-slideshow {
      background-color: #fff;
      border-top: 6px solid $color-listing-border;
      margin-bottom: 80px;

      .field-name-field-media {
        @include inline-block;
        width: 60%;
      }

      .group-content {
        @include inline-block;
        padding-top: 15px;
        width: 39%;

        .field-name-field-title {
          font-family: $font-family-sans;
          color: $background-body;
          margin-bottom: 20px;
        }

        .field-name-field-body {
          font-size: rem-calc(18px);
        }
      }

      .flex-direction-nav {
        display: none;
      }
    }

    .field-name-field-research-quote,
    .field-name-field-faculty-quote {
      @include clearfix;

      .field-name-field-image {
        float: right;
        margin-left: 20px;

        +cite+.field-name-field-body,
        +.field-name-field-body {
          //           padding-right: 0;

          &::after {
            display: none;
          }
        }
      }

      .field-name-field-title {
        font-family: $font-family-sans;
        font-size: rem-calc(21px);
        border-bottom: 2px solid $color-listing-border;
          display: block;
          font-style: normal;
          margin-bottom: 1.2em;
          text-transform: uppercase;
      }
    }

    .group-faculty {
      @include background-pattern;
      margin-top: 0px;

      .field-name-field-title {
        font-family: $font-family-sans;
        font-size: rem-calc(21px);
        border-bottom: 2px solid $color-listing-border;
        display: block;
        font-style: normal;
        margin-bottom: 1.2em;
        text-transform: uppercase;
      }
    }

    .field-name-field-related-programs {
      list-style-image: none;
      list-style-type: none;

      li {

        display: inline-block;
        width: 18.49%;
        margin: 0 0.65%;
        a {
          color: #20438f;
            @include background-pattern;
            font-family: $font-family-sans;
            border-top: 6px solid #2B9FCD;
            @include inline-block;
            min-height: 100px;
            padding: 15px 20px;
            min-height: 135px;
            width: 100%;
        }

        &:first-child {
          margin-left: 0,
        }

        &:last-child {
          margin-right: 0,
        }
      }
    }
  }
}

@media (max-width: 1171px) {
  .field-name-field-related-programs {
