@media (min-width: 768px) and (max-width: 1280px) {
  .node-program.view-mode-full {
    .field-name-field-fact-number {
      //       font-size: rem-calc(80px);
      font-size: rem-calc(58px);
    }
  }
}

@media (min-width: 768px) and (max-width: 1080px) {
  .node-program.view-mode-full {
    .field-name-field-research-slideshow {
      .field-name-field-media {
        width: 100%;
        padding-right: 0;
      }

      .group-content {
        width: 100%;
        padding-left: 2%;
      }
    }
  }
}

@media (max-width: 768px) {
  .node-program.view-mode-full .field-name-field-research-slideshow .field-name-field-media {
    padding-right: 0;
    padding-left: 0;
  }

  .node-program {
    &.view-mode-full {

      .group-top-row,
      .group-related-programs,
      .program-section {
        padding-left: 15px;
        padding-right: 15px;
      }

      .program-section>h3 {
        //         left: 15px;
        position: relative;
        top: -1.4em;
        left: 0;

        span {
          display: block;
        }
      }
    }
  }

  .node-program.view-mode-full .group-body.program-content-width {
    width: 100%;
  }

  .node-program.view-mode-full .program-content-width+.field-name-field-body-slideshow {
    width: 100%;
    margin-bottom: 40px;
  }

  .field-name-field-body-slideshow .flexslider .flex-control-paging {
    bottom: -40px;
    margin: 0 auto;
    padding-left: 5px;
    left: 50%;
  }
}

@media (max-width: 768px) {
  .node-program.view-mode-full .program-content-width+.field-name-field-body-slideshow {
    margin-bottom: 40px;
  }
}