@import 'fa/fontawesome';
@import 'fa/solid';

@font-face {
  font-family: 'futura-pt';
  src: url('../fonts-brand/FuturaPT-Book_gdi.eot');
  src: url('../fonts-brand/FuturaPT-Book_gdi.eot?#iefix') format('embedded-opentype'),
    url('../fonts-brand/FuturaPT-Book_gdi.woff2') format('woff2'),
    url('../fonts-brand/FuturaPT-Book_gdi.woff') format('woff'),
    url('../fonts-brand/FuturaPT-Book_gdi.ttf') format('truetype'),
    url('../fonts-brand/FuturaPT-Book_gdi.svg#futurapt_book') format('svg');
  font-weight: 400;
  font-style: normal;
} 

@font-face {
  font-family: 'futura-pt';
  src: url('../fonts-brand/FuturaPT-Heavy_gdi.eot');
  src: url('../fonts-brand/FuturaPT-Heavy_gdi.eot?#iefix') format('embedded-opentype'),
    url('../fonts-brand/FuturaPT-Heavy_gdi.woff2') format('woff2'),
    url('../fonts-brand/FuturaPT-Heavy_gdi.woff') format('woff'),
    url('../fonts-brand/FuturaPT-Heavy_gdi.ttf') format('truetype'),
    url('../fonts-brand/FuturaPT-Heavy_gdi.svg#futurapt_heavey') format('svg');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'GalliardPro-Roman';
  src: url('../fonts-brand/GalliardPro-Roman_gdi.eot');
  src: url('../fonts-brand/GalliardPro-Roman_gdi.eot?#iefix') format('embedded-opentype'),
    url('../fonts-brand/GalliardPro-Roman_gdi.woff') format('woff'),
    url('../fonts-brand/GalliardPro-Roman_gdi.ttf') format('truetype'),
    url('../fonts-brand/GalliardPro-Roman_gdi.svg#GalliardPro-Roman') format('svg');
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  unicode-range: U+0020-25CA;
}

$font-family-sans: 'futura-pt', Arial, Verdana, Tahoma, sans-serif;
$font-family-serif: 'GalliardPro-Roman', serif;

$font-awesome: 'Font Awesome 5 Free';
$font-size: rem-calc(21px);
$font-size-small: 11px;
$font-size-large: 23px;

