// opacity
@mixin opacity($level: 0.5) {
  opacity: $level;
}

$mobile-max: $tablet - 1;
$tablet-max: $desktop - 1;


// box sizing
@mixin box-sizing($size: border-box) {
  box-sizing: $size;
}

@mixin bordered($top-color: #EEE, $right-color: #EEE, $bottom-color: #EEE, $left-color: #EEE) {
  border-top: solid 1px $top-color;
  border-left: solid 1px $left-color;
  border-right: solid 1px $right-color;
  border-bottom: solid 1px $bottom-color;
}

// border radius
@mixin border-radius($radius: 2px) {
  border-radius: $radius;
}

@mixin border-radius($topright: 0, $bottomright: 0, $bottomleft: 0, $topleft: 0) {
  border-top-right-radius: $topright;
  border-bottom-right-radius: $bottomright;
  border-bottom-left-radius: $bottomleft;
  border-top-left-radius: $topleft;
  @include background-clip(padding-box);
}

// box shadow
@mixin box-shadow($x, $y, $blur, $color) {
  box-shadow: $arguments;
}

@mixin inner-shadow($horizontal:0, $vertical:1px, $blur:2px, $alpha: 0.4) {
  box-shadow: inset $horizontal $vertical $blur rgba(0, 0, 0, $alpha);
}

@mixin drop-shadow($x-axis: 0, $y-axis: 1px, $blur: 2px, $alpha: 0.1) {
  box-shadow: $x-axis $y-axis $blur rgba(0, 0, 0, $alpha);
}

// outline
@mixin outline($style) {
  -moz-outline-style: $style;
  outline: $style;
}

// background
@mixin background($color, $image, $repeat, $scroll, $x, $y) {
  background: $color url($image) $repeat $scroll $x $y;
}

@mixin background-image($image) {
  background: transparent url($image) no-repeat scroll 0 0;
}

// text shadow
@mixin text-shadow($h-shadow, $v-shadow, $blur, $color) {
  text-shadow: $color $h-shadow $v-shadow $blur;
}

// gradient
@mixin gradient($color: #F5F5F5, $start: #EEE, $stop: #FFF) {
  background: $color;
  background-image: linear-gradient(to bottom, $start 0%, $stop 100%);
}

@mixin bw-gradient($color: #F5F5F5, $start: 0, $stop: 255) {
  background: $color;
  background-image: linear-gradient(to bottom, rgb($start, $start, $start) 0%, rgb($stop, $stop, $stop) 100%);
}

@mixin linear-gradient($start, $end) {
  background: $start;
  background-image: linear-gradient($start, $end);
}

@mixin transform($arguments) {
  transform: $arguments;
}

@mixin translate($x: 0, $y: 0) {
  transform: translate($x, $y);
}

@mixin scale($ratio: 1.5) {
  transform: scale($ratio),
}

@mixin rotation($deg: 5deg) {
  transform: rotate($deg),
}

@mixin user-select($argument: none) {
  user-select: $argument;
}

// background clip
@mixin background-clip($type: padding-box) {
  background-clip: $type;
}

// transitions
@mixin transition($duration: 0.2s, $ease: ease-out) {
  transition: all $duration $ease;
}

@mixin transition-duration($duration: 0.2s) {
  transition-duration: $duration;
}

@mixin inline-block($percent: auto) {
  display: inline-block;
  width: $percent;
  padding-right: 2%;
  box-sizing: border-box;
  vertical-align: top;
  zoom: 1;

  .lt-ie8 & {
    display: inline;
  }

  &.last {
    padding-right: 0;
  }
}