  overflow: auto;
  margin-bottom: 15px;

  .views-row {
    padding-top: 35px;
    position: relative;

    .footer-contact-item {
      padding: 0 20px 15px;
    }

    width: 48%;
    float: left;

    .field-name-field-contact-link {
      left: 20px;
      position: absolute;
      top: -15px;

