.view {
  @include view-style;
}

.news-list-view {
  @include listing-view-style;
}

.list-grid-view {
  @include grid-view-style;
}

.view.list-grid-image-event-view {
  @include grid-image-event-view-style;
}

.view-event-calendar {
  .date-repeat-rule {
    display: none;
  }
}

.view-new-event-calendar {
  .date-repeat-rule {
    display: none;
  }
}

.view-events {
  @include event-view-style;

  .region-footer & {
    @include event-footer-view-style;
  }

  &.list-grid-view .views-row .field-type-image {
    margin: 0;
  }
}

.view-display-id-block_7 .views-field-field-banner img {
  border-bottom: 6px solid #cccccc;
  height: auto;
  width: 100%;
}

.view-display-id-block_7 .views-field-field-banner a {
  border-bottom: none;
}

.news-page-view {
  .item-list .pager {
    text-align: right;
  }
}

.view-pomona-news .view-content {
  .views-row .node-teaser a.more-link {
    background-color: none;
