.view-a-z-directory,
.view-office-department {

  .view-content {
    padding-top: 28px;
    width: 72.6%;
  }

  .view-filters,
  .attachment-before {
    clear: right;
    float: right;
    width: 274px;

    .view-header {
      font-size: rem-calc(16px);
      font-family: $font-family-sans;
      margin-bottom: 30px;
      width: auto;
    }

    .view-content {
      padding-top: 0px;
      width: auto;
    }
  }

  .view-filters {
    label {
      font-family: $font-family-sans;
      font-size: rem-calc(21px);
      color: $color-font;
      margin-bottom: 20px;
      text-transform: uppercase;
    }

    form .form-item.form-type-textfield {
      width: 100%;
    }

    input[type='text'] {
      padding: 0 12px;
      border: 5px solid #3FAD70;
      height: 38px;
      width: 100%;
    }

    .views-exposed-form .views-exposed-widget {
      float: none;
      padding: 0;
    }

    .views-widget-filter-keys {
      position: relative;
    }

    .views-widget {
      width: 100%;
    }

    .views-submit-button,
    form {
      position: relative;
      line-height: 0;
      height: auto;

      .form-submit,
      .form-submit:active {
        background: url("../../images/bkg-directory-search-icon.png") 50% 50% no-repeat #96D837;
        color: #fff;
        font-size: 0;
        height: 30px;
        line-height: 0;
        padding: 0;
        position: absolute;
        right: 5px;
        top: -34px;
        width: 31px;
      }
    }
  }

  .views-summary {
    span {
      @include font($size: 14px, $line-height: 2.214, $weight: 700, $family: $font-family-sans);
      display: inline-block;
      height: 31px;
      margin-bottom: 10px;
      text-align: center;
      vertical-align: top;
      width: 12.6%;

      &:hover a,
      a.active {
        background-color: $background-pagination-hover;
        color: #fff;
        height: 100%;
      }

      a {
        color: $color-font;
        display: block;
      }

      &.no-result {
        color: #cccccc;
      }

      &.count {
        display: none;
      }
    }
  }
}

.view-a-z-directory {
  .view-header {
    font-size: rem-calc(36px);
    float: left;
    margin-bottom: 55px;
    width: 72.6%;
  }

  .view-filters .views-exposed-form {
    margin-bottom: 10px;
  }

  .view-content {
    clear: left;

    .item-list {
      border-top: 6px solid #ffe7b6;
      display: inline-block;
      margin-bottom: 40px;
      margin-right: 3.7%;
      padding-left: 35px;
      padding-top: 30px;
      position: relative;
      vertical-align: top;
      width: 46.25%;

      h3 {
        @include filtered-list-header-style;
      }

      .views-row {
        border: 0px none;

        a {
          font-family: $font-family-sans;
          @include list-link-style;
        }
      }
    }
  }
}

.view-directory {
  .view-content {
    .group-wrapper {
      border-top: 6px solid #ffe7b6;
      margin-bottom: 45px;
      padding-left: 45px;
      padding-top: 30px;
      position: relative;
    }

    h3 {
      @include filtered-list-header-style;
      z-index: 2;
    }

    .views-row {
      .field-name-field-listing-page.field-type-link-field {
        a {
          font-family: $font-family-sans;
font-weight: 700;
          font-weight: normal;
          font-size: rem-calc(14px);
          line-height: 1.6em;
          background-color: #af74b1;
          color: #fff;
          display: inline-block;
          letter-spacing: 1px;
          padding: 5px 28px 5px 10px;
          position: absolute;
          right: 20px;
          bottom: -12px;
          text-transform: uppercase;
          border: none;
          cursor: pointer;
          z-index: 100;

          &:hover {
            text-decoration: none;
          }

          &:after {
            color: #ffe121;
            content: "\f054";
            font-family: "FontAwesome";
            font-size: rem-calc(12px);
            line-height: 12px;
            margin-top: -6px;
            position: absolute;
            right: 9px;
            top: 50%;
          }
        }
      }

      font-family: $font-family-sans;
      font-size: rem-calc(18px);
      border-top: 0px none;
      border-bottom: 2px solid $color-listing-border;
      color: $color-form-text;
      margin-bottom: 30px;
      padding-bottom: 20px;
      position: relative;
      z-index: 1;

      &.views-row-last {
        border-bottom: 0px none;
      }

      a {
        @include list-link-style;
      }

      .directory-row-department,
      .views-field-title {
        font-size: rem-calc(21px);
        margin-bottom: 20px;

        a {
          display: inline-block;
        }
      }

      .directory-row-col-1,
      .directory-row-col-2 {
        display: inline-block;
        line-height: 1.3em;
        vertical-align: top;
        width: 30%;
      }

      .directory-row-col-1 {
        width: 59%;

        p {
          margin-bottom: 10px;
        }
      }

      .field-name-field-campus-location {
        margin-bottom: 13px;
      }

      .field-name-field-primary-contact {
        margin-bottom: 3px;
      }
    }
  }
}

.directory-form-wrapper {
  background: url("../../images/bkg-directory-filter.jpg") repeat-x 0 0 transparent;
  margin: 0 -75px 85px;
  min-height: 141px;
  overflow: hidden;
  padding: 30px 75px 0px;

  form {
    color: #fff;

    input[type="search"],
    input[type="text"],
    input[type="email"],
    input[type="password"] {
      display: inline-block;
      padding: 8px 16px;
      vertical-align: top;
      width: 78.4%;

      &:focus {
        padding: 8px 16px;
      }
    }
  }

  .views-exposed-form .views-exposed-widget {
    margin-bottom: 12px;
  }

  .views-widget-filter-field_person_name_family,
  .first-name-wrapper {
    clear: right;
    display: inline-block;
    float: none;
    margin-bottom: 20px;
    margin-right: 5%;
    vertical-align: top;
    width: 38.3%;

    label {
      line-height: 41px;
    }
  }

  .views-widget-filter-field_school_role_value,
  .last-name-wrapper {
    clear: both;
    display: inline-block;
    margin-right: 2%;
    width: 38.3%;
    vertical-align: top;

    label {
      line-height: 41px;
    }
  }

  .types-wrapper {
    display: inline-block;
    margin: 0 auto;
    vertical-align: top;
    width: 90%;

    .form-type-bef-checkbox {
      display: inline-block;
    }

    span {
      display: inline-block;
      margin-right: 15px;

      label {
        display: inline-block;
        float: none;
        margin-right: 0;
      }
    }
  }

  label {
    float: left;
    margin-right: 3.2%;
  }

  input[type="submit"] {
    background: url("../../images/bkg-directory-search-icon.png") 50% 50% no-repeat #96D837;
    color: transparent;
    font-size: 0;
    height: 30px;
    line-height: 0;
    padding: 0;
    width: 31px;
  }
}

.view-people-directory {
  .view-content {
    .group-wrapper {
      padding-bottom: 30px;
    }

    .views-row {
      border: 0px none;
      display: inline-block;
      vertical-align: top;
      width: 35%;
    }
  }
}

.directory-wrapper {
  .letter {
    border-top: 6px solid #ffe7b6;
    margin-bottom: 45px;
    padding-left: 45px;
    padding-top: 30px;
    position: relative;

    h2 {
