}

.view-pomona-news {
  .page-views & {
    .slide-container {
      margin-bottom: 0;

      .field-name-field-image {
        margin-right: 0;
      }
    }

    .views-row {
      .field-name-field-publish-date {
        margin-bottom: 10px;
      }

      .field-name-body {
        a.more-link {
          background-color: transparent;
          color: #158BB9;
          font-size: .7em;
          padding: 5px 25px 5px 5px;

          &:after {
            color: #ffe121;
            font-size: .7em;
          }
        }
      }

      .field-name-field-image {
        img {
          width: auto;
        }
      }

      .links.inline {
        @include element-invisible;
      }
