.page-node-14 {
  .view-event-calendar {
    @include background-pattern;
    padding: 45px 20px 20px;

    .view-content {
      background: none;
      padding: 0;
    }
  }
}

.view-event-calendar,
.view-new-event-calendar {
  .audience-list {
    font-size: rem-calc(16px);
    line-height: 1.6em;
    color: #000;
    display: block;
  }

  font-family: $font-family-sans;

  .view-content {
    @include background-pattern;
    padding: 45px 20px 20px;
  }

  .view-header {
    position: relative;

    .date-nav-wrapper .item-list .pager {
      li {
        margin: 0;
        padding: 0;

        a {
          color: transparent;
          font-size: 0;
          margin-left: 0;

          &:before {
            background: none;
            color: $color-chevron;
            content: "\f054";
            font-family: "FontAwesome";
            font-size: rem-calc(26px);
            position: absolute;
          }

          &:hover {
            background-color: transparent;
            opacity: 0.7;
          }
        }

        &.date-prev {
          left: 0;
          right: inherit;

          a:before {
            left: 0;
            -webkit-transform: rotate(180deg);
            -moz-transform: rotate(180deg);
            -ms-transform: rotate(180deg);
            -o-transform: rotate(180deg);
            transform: rotate(180deg);
          }
        }
      }
    }
  }

  .views-row {
    padding: 12px 20px 20px;
    position: relative;
    min-height: 100px;

    .repeating-dates-row {
      min-height: 120px;

      .field-event-date {
        max-width: 240px;
        padding-left: 0;
        text-align: right;

        span {
          display: none;
        }

        .date-repeat-rule {
          display: block;
        }
      }
    }

    span.views-label {
      font-size: rem-calc(16px);
      color: $color-form-text;
      float: left;
      margin-right: 5px;
    }

    .field-type-taxonomy-term-reference {
      font-size: rem-calc(16px);
      color: $color-font;
      text-transform: none;
    }

    .field-name-field-event-date,
    .views-field-events-month-display,
    .field-event-date {
      font-size: rem-calc(21px);
      color: $color-font;
      float: right;
      padding-left: 30px;
      position: absolute;
      right: 20px;
      top: 15px;

      &:after {
        color: $color-yellow-accent;
        content: "\f017";
        font-family: "FontAwesome";
        font-size: rem-calc(24px);
        left: 0;
        position: absolute;
        top: 0;
      }
    }
  }

  .views-field.views-field-title {
    display: inline-block;
    margin-right: 3%;
    width: 30.5%;
    vertical-align: top;
  }

  .middle-col {
    display: inline-block;
    width: 35%;
    vertical-align: top;
  }

  .views-field-nothing-1 {
    bottom: -10px;
    position: absolute;
    right: 20px;

    // Adjust for the month / search view.
    @at-root .view-display-id-events &,
      .view-display-id-page_2 & {
      bottom: -15px;
    }
  }

  &.view-event-calendar-week-month {
    .view-header {
      margin-bottom: 20px;
      position: relative;

      .date-nav-wrapper {
        margin-top: 0;

        .date-nav {
          margin-bottom: 0;
          padding-bottom: 0;

          .date-heading h3 {
            font-family: $font-family-sans;
font-weight: 700;
            text-transform: uppercase;
            color: #018abb;
            font-size: rem-calc(18px);
            font-style: normal;
            margin-top: 10px;
          }
        }
      }
    }

    .view-content {
      padding-top: 20px;
    }

    .group-wrapper h3,
    .view-content h3 {
      text-align: center;
      margin-bottom: 45px;
    }

    .views-row-last {
      margin-bottom: 70px;
    }
  }

  .horizontal-tabbed-menu {
    display: none;
  }

}

#views-exposed-form-event-calendar-page #edit-field-sub-category-tid-wrapper ul li {
  list-style: none;
}

.section-global-pomona {
  .highlighted-slideshow-block {
    .flex-control-paging {
      bottom: 220px;
    }
  }
}

@media (max-width: 1300px) {}

@media (max-width: 1080px) {
  .directory-form-wrapper {
    background: none $color-link-listed;
    padding-bottom: 20px;
  }
}


@media (max-width: 1075px) {

  .view-a-z-directory,
  .view-office-department {

    .view-content {
      width: 100%;
    }

    .view-filters,
    .attachment-before {
      float: none;
    }
  }
}

@media (min-width: 768px) and (max-width: 1300px) {

  .view .views-row .field-thumbnail img,
  .view .views-row .field-name-field-image img {
    height: auto;
    width: 100%;
  }
}

@media (max-width: 1024px) {
  .view-events {
    .views-row {
      min-height: 240px;
    }
  }
}

@media (max-width: 959px) {
  .view-events {
    .views-row {
      min-height: 260px;
    }
  }

  .directory-form-wrapper input[type="submit"] {
    margin-top: 47px;
  }

  .view-events.list-grid-view {
    padding: 0 15px;
  }
}

@media (max-width: 768px) {
  .section-global-pomona {
    .highlighted-slideshow-block {
      .flex-control-paging {
        top: 40%;
        left: 40px;
      }
    }
  }

  .view-events {
    .views-row {
      min-height: inherit;
    }
  }

  .page-node .view {
    .views-row {

      .field-name-title-field,
      .field-name-body {
        padding: 0;
      }
    }
  }

  .view-events.list-grid-view {
    padding: 0;
  }

  .region-footer {
    .view-events {
      .views-row .field-type-image img {
        width: 100%;
      }

      .views-row-2,
      .views-row-5,
      .views-row-8,
      .views-row-11 {
        margin-left: 0;
        margin-right: 0;
      }
    }
  }

  .view.view-events {
    .views-row {
      width: 100%;

      .field-name-field-image {
        float: none;
        margin-right: 0;
      }
    }

    .views-row-odd {
      margin-right: 0;
    }
  }

  .view-pomona-news {
    ul.pager {
      width: 100%;
      text-align: center;
    }

    .view-footer {
      text-align: center;
    }

    .field-name-field-image {
      width: 100%;

      img {
        width: 100%;
      }
    }

    .field-name-title-field,
    .field-name-field-publish-date,
    .field-name-body {
      padding: 0 10px 0 10px;
    }
  }

  .view-event-calendar,
  .view-new-event-calendar {
    .views-field.views-field-title {
      display: block;
      margin-bottom: 15px;
      margin-right: 0;
      width: 100%;
    }

    .middle-col {
      display: block;
      margin-bottom: 15px;
      width: 100%;
    }

    .views-row {
      padding-bottom: 45px;

      .field-name-field-event-date,
      .field-event-date {
        float: none;
        position: relative;
        right: 0;
        top: 0;
      }
    }

    .horizontal-tabbed-menu {
      display: block;
    }
  }

  // Hide the events menu block, since it's contained within the view,
  // shown above with .horizontal-tabbed-menu
  .events-menu-block {
    display: none;
  }

  .news-list-view {

    .group-listing-info {
      margin-left: 0;
      padding: 0 15px;
    }

    .field-title,
    .field-name-title-field {
      margin-left: 0px;
    }

    .field-name-body {
      margin-left: 0px;
    }

    .field-thumbnail,
    .field-name-field-image {
      float: none;
      margin-bottom: 20px;
      margin-right: 0px;

      img {
        max-width: 100%;
      }
    }

    .field-name-field-event-date,
    .field-name-field-publish-date {
      margin-left: 0px;
      padding: 0;
    }
  }

  .list-grid-view {
    .views-row {
      display: block;
      width: 100%;
      min-height: auto;

      .field-type-image {
        float: none;

        img {
          display: block;
        }
      }

      .field-name-title-field {
        margin-bottom: 15px;
      }

      .field-type-datetime {
        margin-bottom: 15px;
      }

      .group-inner-wrapper {
        padding: 10px 20px 0px;
      }
    }

    .views-row-even {
      margin-left: 0;
      margin-right: 0;
    }
  }

  .directory-form-wrapper input[type="submit"] {
    margin-top: 0px;
  }

  .view-directory .view-content .views-row {

    .directory-row-col-1,
    .directory-row-col-2 {
      width: 100%;
    }
  }

  .view .date-nav-wrapper .date-heading h3 {
    line-height: 1.3em;
  }
}

.highlight-slideshow-hide-pager .flex-control-paging {
  display: none;
}

@media (max-width: 550px) {
  .section-global-pomona {
    .highlighted-slideshow-block {
      .flex-control-paging {
        top: 30%;
