@-ms-viewport{
  width: device-width;
}

.visually-hidden { // "Joe Watkin" solution described here: https://zellwk.com/blog/hide-content-accessibly/
  @include visually-hidden;
}

[hidden] { // Enable hidden for IE < 11
  display: none;
}

// Legacy styles
.block {
  margin-bottom: 1.5em;
}

// Fix double contextual links where they get rendered once for the block
// contextual links and then again for the view mode of the block node
.block-nodeblock > .contextual-links-wrapper + .node > .contextual-links-wrapper {
  display: none;
}

.region-header {
  clear: both;
}

.ease {
  @include transition(all .2s ease);
}

.hidden-text {
  font-size: 0;
  line-height: 0;
  text-indent: -1000px;
  overflow: hidden;
  position: relative;
  text-align: left;
}

@media (min-width: $tablet) {
  .mobile-only {
    display: none;
  }
}
.no-list-style {
	list-style-type: none;
	list-style-image: none;
}

.no-appearance {
  appearance: none;
}

.no-outline {
  outline: none;
}

.no-border-radius {
  border-radius: 0;
}

.no-box-shadow {
  box-shadow: none;
}

.container-inline {
  div,
  label {
    display: inline;
  }
}

.container-inline-block {
  & > * {
    @include inline-block;
  }

  &.column-4 {
    & > div {
      @include inline-block(25%);
    }
  }
  &.column-3 {
    & > div {
      @include inline-block(32%);
    }
  }
  &.column-2 {
    & > div {
      @include inline-block(49%);
    }
  }
}

p {
  margin: 0 0 1.2em;
}

// Headings
h1,
h2,
h3,
h4,
h5,
h6 {
  text-rendering: optimizelegibility;
}
h1 {
  &.title {
    margin: 0;
  }
}
h2 {
  &.comment-form,
  &.title,
  &.block-title,
  &.node-title {
    margin: 0;
  }
}
h3 {
  &.title {
    margin: 0;
  }
}

// Form Styling
form {
  .form-item {
    margin: 1.5em 0;

    label {
      display: block;
      font-weight: bold;
    }

    label.option {
      display: inline;
      font-weight: normal;
    }

    input.error,
    textarea.error,
    select.error {
      border: 1px solid c('red1');
    }

    .description {
      font-size: rem-calc(14px);
    }
    .form-textarea,
    .form-text {
      @include border-radius(5px);
    }
    .form-textarea {
      resize: none;
    }
    .grippie {
      display: none;
    }
  }


  .form-required {
    color: c('red1');
  }

  .form-radios,
  .form-checkboxes {
    .form-item {
      margin: 0;
    }
  }

  // Placeholders
  input:-moz-placeholder,
  textarea:-moz-placeholder {
    color: $color-placeholder;
  }
  input:-ms-input-placeholder,
  textarea:-ms-input-placeholder {
    color: $color-placeholder;
  }
  input::-webkit-input-placeholder,
  textarea::-webkit-input-placeholder {
    color: $color-placeholder;
  }

  // Password confirmation
  .password-parent,
  .confirm-parent {
    margin: 0;
  }
}

// Labels
span.field-label {
  padding: 0 1em 0 0;
}


// Links

// List Styles
ul {
  li.expanded {
    list-style-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAAHAQMAAAD+nMWQAAAABlBMVEX///8AAABVwtN+AAAAAXRSTlMAQObYZgAAABJJREFUeJxj+MdQw2DBIMAABgAUsAHD3c3BpwAAAABJRU5ErkJggg==');
    list-style-type: circle;

    .lt-ie8 & {
      list-style-image: url('../images/menu-leaf.png');
    }
  }
  li.collapsed {
    list-style-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAAHAQMAAAD+nMWQAAAABlBMVEX///8AAABVwtN+AAAAAXRSTlMAQObYZgAAABFJREFUCB1jVmCGQClmEWYOAAZ8AMy3HPLXAAAAAElFTkSuQmCC'); /* LTR */
    list-style-type: disc;

    .lt-ie8 & {
      list-style-image: url('../images/menu-collapsed.png');
    }
  }
  li.leaf {
    list-style-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAAHBAMAAAA2fErgAAAAD1BMVEX///+/v7+Li4sAAADAwMBFvsw8AAAAAXRSTlMAQObYZgAAAB1JREFUCFtjYAADYwMGBmYVZSDhKAwkFJWhYiAAAB2+Aa/9ugeaAAAAAElFTkSuQmCC');
    list-style-type: square;

    .lt-ie8 & {
      list-style-image: url('../images/menu-expanded.png');
    }
  }

  &.inline {
    display: inline;
    padding: 0;

    li {
      display: inline;
      padding: 0 1em 0 0;
    }
  }
}
// Node
.comment-unpublished,
.node-unpublished {
  p.unpublished {
    height: 0;
    overflow: visible;
    color: c('gray2');
    font-size: rem-calc(75px);
    line-height: 1;
    font-family: Impact, "Arial Narrow", Helvetica, sans-serif;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    word-wrap: break-word;

    .lt-ie8 & > * {
      position: relative;
    }
  }
}

// Breadcrumb
.breadcrumb {
  ol {
    margin: 0;
    padding: 0;
  }
  li {
    display: inline;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
}

// Messages
div.messages {
  margin: 1.5em 0;

  ul {
    margin-top: 0;
    margin-bottom: 0;
  }
}

// Pagers
.item-list .pager {
  padding: 0;

  li {
    padding: 0 0.5em;
  }
}

// Admin Tabs
ul.primary,
ul.secondary {
  border-bottom: 1px solid c('gray3');
  list-style: none;
  margin: 1em 0 1.5em;
  padding: 0 2px;
  white-space: nowrap;

  .lt-ie8 & {
    zoom: 1;
  }

  li {
    float: left;
  }

  a {
    display: block;
    line-height: 1.5em;
    text-decoration: none;
  }
}
ul.primary {
  padding: 0;

  &:after,
  &:before {
    display: table;
    line-height: 0;
    content: "";
  }
  &:after {
    clear: both;
  }

  li.active {
    margin-bottom: -1px;

    a {
      border: 1px solid c('gray3');
      border-bottom: 0 none;
      background-color: c('white1');
      color: $color-font;
    }

    .lt-ie8 & {
      zoom: 1;
    }
  }

  a {
    @include border-radius(4px, 4px, 0, 0);

    &:link,
    &:visited {
      padding-right: 12px;
      padding-left: 12px;
      padding-top: 8px;
      padding-bottom: 8px;
      margin-right: 2px;
      line-height: 20px;
      letter-spacing: 1px;
      text-align: center;
    }

    &:hover,
    &:focus {
      background-color: c('gray4');
      border-color: c('gray4');
    }
  }
}
ul.secondary {
  font-size: rem-calc(14px);
  margin-top: -1.5em;
  padding-bottom: .5em;

  a {
    &:link,
    &:visited {
      @include border-radius(0.75em);
      @include transition(background-color 0.3s);
      @include text-shadow(white, 1px, 1px, 0);
      background-color: #f2f2f2;
      color: #666;
      padding: 0 .5em;
    }

    &:hover,
    &:focus {
      background-color: #dedede;
      border-color: #999;
      color: #333;
    }

    &.active,
    &:active {
      @include text-shadow(#333333, 1px, 1px, 0);
      background-color: #666;
      border-color: #000;
      color: #fff;
    }
  }
}

// Comments
.comments {
  margin: 1.5em 0;
}

.comment-preview {
  background-color: #ffffea;
}

.comment {
  .new {
    color: #c00;
  }

  .permalink {
    text-transform: uppercase;
    font-size: 75%;
  }
}

.indented {
  margin-left: 30px;
}

// Superfish Menus
.sf-menu {
  li {
    a {
      color: $color-link;
    }

    a:hover,
    &.active-trail a {
      color: $color-link-hover;
    }
  }
}

// magnify button
span.magnify {
  width: 17px;
  height: 17px;
  background: transparent url("../../images/magnify.png") 0 0 no-repeat;
  position: relative;
  padding: 0;
  display: block;
  margin: 0 -6px -12px 0;
  z-index: 2;
  float: right;
}

// Tables
table {
  width: 100%;
  background-color: transparent;
  border-collapse: collapse;
  border-spacing: 0;
}
