// Slideshow

.flexslider {
  .slides {
    img {
      height: auto;
    }
  }
}
.paragraphs-item-slideshow-with-links {
  .group-caption-box {
    background: url("../../images/stripey-bg.png");
    position: absolute;
    bottom: 0;
    width: 100%;
    .group-caption-text-area {
      display: block;
      width: 75%;
      float: left;
    }
    .group-link-region {
      display: block;
      width: 25%;
      float: left;
    }
  }
}
body.page-node-1,
.front {
  .block-slideshow {
    margin-bottom: 0;
  }
}
body.page-node-1,
.front.page-node {
  .field-name-field-banner {
    margin-left: 0;
    margin-right: 0;
  }
}
body.page-node-1,
.front {
  .content-inner-wrapper {
    max-width: 100%;
  }
}

.slide-container-front_page_slideshow {
  .slide-caption-wrapper {
    margin: 0 auto;
    max-width: 900px;

    .slide-caption-inner-wrapper {
      bottom: 15%;
      left: 17%;
      margin: 0 auto;
      max-width: 900px;
      position: absolute;
      min-width: 48%;
    }

    .slide-caption {
      color: #fff;
      font-size: rem-calc(48px);


      .field-name-field-caption {
        background: $color-link-listed;
        line-height: .9em;
        padding: 15px 25px;
      }
    }

    .slide-links {
      padding-left: 30px;
      margin-top: -7px;

      .field-name-field-link a {
        @include icon-button;
        background-color: $color-link !important;
      }
    }

    .flex-control-paging {
      bottom: 55px;

      li {
        margin: 0 3px;
      }
    }
  }
}

.slide-container-simple_content_slideshow,
.view-pomona-news .slide-container,
#block-views-events-block-6 {
  font-family: $font-family-sans;
  margin-bottom: 25px;

  .flex-control-nav {
    display: none;
  }

  .slide-caption-wrapper {
    font-size: rem-calc(24px);
    background: url("../../images/bkg-simple-content-slide.jpg") repeat 0 0 transparent;
    //bottom: 0;
    color: #fff;
    height: 105px;
    line-height: 1.2em;
    position: relative;
    width: 100%;

    .slide-caption {
      padding: 0 60px;
      position: relative;
      top: 50%;
      -webkit-transform: translateY(-50%);
      -moz-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      -o-transform: translateY(-50%);
      transform: translateY(-50%);
    }
    .slide-links {
      position: absolute;
      right: 10%;
      bottom: -20px;
      a {
        @include icon-button;
      }
    }
  }

  .flex-direction-nav {
    a {
      color: transparent;
      display: block;
      font-size: 0;
      height: 25px;
      width: 16px;

      &:before {
        background: none;
        color: $color-chevron;
        content:"\f054" !important;
        font-family: "FontAwesome";
        font-size: rem-calc(26px);
        height: 17px !important;
        left: -7px;
        position: absolute;
        top: 0px;
        width: 24px !important;
      }
    }

    .flex-next,
    .flex-prev {
      opacity: 1 !important;
      top: 38% !important;

      &:hover {
        opacity: 0.7 !important;
      }
    }

    .flex-next {
      right: 15px;
    }

    .flex-prev {
      left: 15px;

      &:before {
        left: -7px;
        top: 8px;
        -webkit-transform: rotate(180deg);
        -moz-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        -o-transform: rotate(180deg);
        transform: rotate(180deg);
      }
    }
  }
}
#block-views-events-block-6 {
  margin-bottom: 55px;
}

.view-slideshow-items.view-display-id-attachment_1 {
  font-family: $font-family-sans;

  .mythumbs {
    text-align: center;
    border-top: 6px solid $color-link;
    padding-top: 6px;

    li {
      font-family: $font-family-sans;
font-weight: 700;
      display: inline;
      border-top: none;
      background: $color-border-bucket-blue;
      margin-right: 10px;
      padding: 9px 15px 13px 20px;
      color: #fff;
      cursor: pointer;
      text-transform: uppercase;
    }

    li.flex-active,
    li:hover {
      background: $color-link-listed;
      padding-top: 15px;
    }
  }
}

.slide-container-detailed_slide {
    .flex-control-nav {
      display: none;
    }
    .flex-direction-nav .flex-prev {
      left: 1% !important;
      top: 35%;
    }
    .flex-direction-nav .flex-next {
      right: 1% !important;
      top: 35%;
    }
  .slide-caption-wrapper {
    position: absolute;
    bottom: 0;
    width: 100%;
    background: url("../../images/stripey-bg.png");
    min-height: 190px;
    padding: 35px 80px 20px;
    background-size: cover;
    .slide-caption {
      width: 70%;
      float: left;
      color: #fff;
      padding-right: 7%;
      .field-name-field-caption-title {
        font-size: rem-calc(54px);
        line-height: 1em;
        margin-bottom: 5px;
      }
      .field-name-field-caption {
        line-height: 1.4;
      }
    }
    .slide-links {
      width: 20%;
      float: right;
      font-family: 'futurapt_heavy', Arial, Verdana, Tahoma, sans-serif;
      font-weight: normal;
      font-size: rem-calc(14px);
      line-height: 1.6em;
      margin: 0 0 12px;
      padding: 0;
      list-style: none;
      margin-bottom: 0px;
      border: 0px none;
      .field-name-field-slide-url {
        margin-bottom: 15px;

        &:last-child {
          margin-bottom: 0px;
        }
      }
      a {
        color: #fff;
        background: #b072b3;
        font-weight: normal;
        display: block;
        line-height: 14px;
        padding: 9px 30px 9px 13px;
        position: relative;
        text-decoration: none;
        -moz-text-decoration-line: none;
        text-transform: uppercase;
        &:after {
          color: #ffe121;
          content: "\f054";
          font-family: "FontAwesome";
          font-size: rem-calc(12px);
          line-height: 12px;
          margin-top: -6px;
          position: absolute;
          right: 4.5%;
          top: 50%;
          float: right;
        }
      }
    }
  }
}

@media (max-width: 1300px) {

}

@media (min-width: 768px) and (max-width: 1280px) {
  .slide-container-front_page_slideshow {
    .slide-caption-wrapper {
      .slide-caption-inner-wrapper {
        max-width: 550px;
//         line-height: 2.3em;
      }
    }
  }
  .flex-control-paging {
    bottom: 30px;
    margin-left: 0;
    left: 23%;
  }
}

@media (min-width: 1000px) {
  .flex-control-paging {
    bottom: 45px;
    margin-left: 0;
    left: 23%;
  }
}

@media (max-width: 959px) {
  .slide-container-front_page_slideshow {
    .slide-caption-wrapper {
      .slide-caption {
        .field-name-field-caption {
          white-space: normal;
        }
      }
    }
  }

  .slide-container-detailed_slide .slide-caption-wrapper {
    .slide-caption {
      padding-right: 0;
      width: 72%;
    }

    .slide-links {
      width: 25%;
    }
  }
}

@media (max-width: 768px) {
  .slide-container-simple_content_slideshow,
  .view-pomona-news .slide-container,
  #block-views-events-block-6 {
    .slide-caption-wrapper .slide-caption {
      padding: 0 50px;
    }
  }
}
